import React, { useEffect, useState } from 'react';
import MaterialTable from 'material-table';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Link} from 'react-router-dom';
import ButtonNew from './../Buttons/ButtonNewRegistro';
import MenuPopupState from './../../MenuGrids/MenuMaterialTableCatalogos'
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import BrightnessAutoIcon from '@material-ui/icons/BrightnessAuto';
import ipInfoLab from '../../containers/Functions/GetIpInfolab';
import Axios from 'axios';
import { getPostsPedido } from './../../utils/RestPedido';
import ComboBoxAlmacen from './../../containers/ComboBox-Data/ComboBoxAlmacen'
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import Loader from 'react-loader-spinner'
import Collapse from '@material-ui/core/Collapse';
import { Alert, AlertTitle } from '@material-ui/lab';
import Chip from '@material-ui/core/Chip';
import { getPostsAlmacen } from './../../utils/RestAlmacen';
import useToken from './../Login/useToken';
import {GetPermisosAccionPantallaPerfil} from './../../containers/Functions/GetPermisosAccionPantallaPerfil';




const useStyles = makeStyles((theme) => ({


  ButtonPedidoAutomatico: {
    margin: theme.spacing(1),
    backgroundColor: '#ecf6fd',
    border: 0,
    fontWeight: 'bold',
    color: '#2797ef',
    textTransform: 'none'
  },

  root: {
    justifyContent: 'center',
    flexWrap: 'wrap',
    '& > *': {
      margin: theme.spacing(0.5),
    },
  },

  

}));

const MaterialTablePedido = React.memo((props) =>
{
   
    const {posts} =  props;
    const [loading, setloading] = useState(false)
    const [openPedidoAutomatico, setOpenPedidoAutomatico] = React.useState(false);
    const [scroll, setScroll] = React.useState('paper');
    const descriptionElementRef = React.useRef(null);
    const [dataMaterialTable, setdataMaterialTable] = useState([])
    const [AlmacenId, setAlmacenId] = useState(0)
    const [PedidoIdGenerado, setPedidoIdGenerado ] = useState(0)
    const [openSucces, setOpenSucces] = React.useState(false);
    const [openError, setOpenError] = React.useState(false);
    const [MensajeRest, setMensajeRest] = useState('');

    //Se obtienen los datos del usuario logueado
    const {TokenSdtUsuario} = useToken();
    const {UsuarioId, PerfilId} =  TokenSdtUsuario

    //Variables donde se guardan las acciones permitidas dentro de la pantalla
    
    const [PermisoGENERAPEDIDO, setPermisoGENERAPEDIDO] = useState(false)
    const [PermisoMODIFICAPEDIDO, setPermisoMODIFICAPEDIDO] = useState(false)
    const [PermisoPEDIDOAUTOMATICO, setPermisoPEDIDOAUTOMATICO] = useState(false)
    const [PermisoRECIBIRPEDIDO, setPermisoRECIBIRPEDIDO] = useState(false)


    const classes = useStyles();


    useEffect(() => {
              



      //Se recupera el valor default de almacen[INICIO]

      const AlmacenFilters = {
        AlmacenId : 0,
        AlmacenNombre: "",
        MostrarDefault: true
      }



      getPostsAlmacen(AlmacenFilters)
      .then((res) => {
        
        const AlmacenId = res.data[0].AlmacenId

        setAlmacenId(AlmacenId)
      })

      //Se recupera el valor default de almacen[FIN]
      
    //Se ingresa el men del registro [INICIO]

      //Servicio para recuperar las acciones permitidas del usuario [INICIO]


      const FiltersPermisos = {
        PerfilId : PerfilId,
        OpcionId : 0,
        OpcionUrl : '/CatalogoPedidos'
      }

      const PermisosMenu =  GetPermisosAccionPantallaPerfil(FiltersPermisos)
      .then((response) => {

        return {response}

      })

      var getPermisos = Promise.resolve(PermisosMenu);

      // var {permisoModificarPedido, permisoRecibirPedido} = false

      getPermisos.then(function(value) {

        const {SDTPerfilUsuarioOpcion} =  value.response.data
        const {Accion} = SDTPerfilUsuarioOpcion

        Accion.map((accion) => {
          const {AccionId, AccionAcceso} = accion

          switch (AccionId) {
            case "GENERAPEDIDO":
              setPermisoGENERAPEDIDO(AccionAcceso)
              break;

            case "MODIFICAPEDIDO":
              // permisoModificarPedido = AccionAcceso
              setPermisoMODIFICAPEDIDO(AccionAcceso)
              break;

            case "PEDIDOAUTOMATICO":
              setPermisoPEDIDOAUTOMATICO(AccionAcceso)
              break;

            case "RECIBIRPEDIDO":
              // permisoRecibirPedido = AccionAcceso 
              setPermisoRECIBIRPEDIDO(AccionAcceso)
              break;
        
            default:
              break;
          }
       
        })

      })
   

        const DataWithMenu = posts.map(Response => {
          const {PedidoId, PedidoEstatus} = Response;
    
          
          //Se arman las opciones que tendr el men delmaterial table
    
          let OpcionesMenuMaterialTable = []

            OpcionesMenuMaterialTable = [
              {
                pathname : "/ViewPedido",
                state : [{
                  Mode : "DSP",
                  CampoId : 'PedidoId',
                  id : PedidoId,
                }],
      
                TituloOpcion : "Visualizar"
                },
            ]
            if ((PedidoEstatus === 2 || PedidoEstatus === 1) && PermisoMODIFICAPEDIDO) {
              let datos = [...OpcionesMenuMaterialTable,
              {
                pathname : "/UpdatePedido",
                 state : [{
                   Mode : "UPD",
                   CampoId : 'PedidoId',
                   id : PedidoId,
                 }],
      
                 TituloOpcion : "Modificar"
              }]
    
              OpcionesMenuMaterialTable = datos
            }
    
            if (PedidoEstatus === 3 && PermisoRECIBIRPEDIDO) {
              let datos = [...OpcionesMenuMaterialTable,
                {
                  pathname : "/RecibirProductosPedido",
                   state : [{
                     Mode : "UPD",
                     CampoId : 'PedidoId',
                     id : PedidoId,
                   }],
        
                   TituloOpcion : "Recibir productos"
                }]
      
                OpcionesMenuMaterialTable = datos
              
            }
          
            return{...Response, DataWithMenu: <MenuPopupState OpcionesMenuMaterialTable= {OpcionesMenuMaterialTable}> </MenuPopupState>}
         
          
        })


        const DataWithStatus = DataWithMenu.map(Response => {

          return{...Response, Status :  <div className={classes.root}>
            {Response.PedidoEstatus == 1 && <Chip label="Abierto" style = {{backgroundColor : "rgb(243, 182, 0)", color:"white"}}/>}
            {Response.PedidoEstatus == 2 && <Chip label="Autorizado" style = {{backgroundColor : "rgb(52, 152, 219)", color:"white"}}/>}
            {Response.PedidoEstatus == 3 && <Chip label="Solicitado" style = {{backgroundColor : "rgb(249, 128, 102)", color:"white"}}/>}
            {Response.PedidoEstatus == 4 && <Chip label="Recibido" style = {{backgroundColor : "rgb(46, 204, 113)", color:"white"}}/>}
            {Response.PedidoEstatus == 5 && <Chip label="Cancelado" style = {{backgroundColor : "rgb(255,0,0)", color:"white"}}/>}
            </div>}
        })
    
    
         setdataMaterialTable(DataWithStatus)
    


        
      // });

      // console.log(cast);
      //Servicio para recuperar las acciones permitidas del usuario [FIN]
      


     //Se arman las opciones que tendr el men delmaterial table [FIN]

    

    }, [props])



    const handleClosePedidoAutomatico = () => {
        setOpenPedidoAutomatico(false);
       
      };

    const GenerarPedidoAutomatico = () =>
    {

      setOpenPedidoAutomatico(true);


    }

    const callbackAlmacenFunction = (AlmacenData) => {
      setAlmacenId(AlmacenData)
    }

    const AceptarGenerarPedidoAutomatico = () => {

      setloading(true)

      const DataPedidoAutomatico = {
        AlmacenId :  AlmacenId, 
        UsuarioId : UsuarioId
      }

     
      const GenerarPedidoEnAutomatico = new Promise((resolve, reject) => {
      const urlRest = ipInfoLab + '/rest/GenerarPedidoAutomatico'

      Axios.post(urlRest, DataPedidoAutomatico).then(response => {

          const PedidoId = response.data.PedidoId
          const MensajeRest = response.data.MensajeRest
          const ExisteError = response.data.ExisteError
          

          setPedidoIdGenerado(PedidoId)
          setMensajeRest(MensajeRest)
          setOpenPedidoAutomatico(false)

          if(ExisteError === 0)
          {
            setOpenSucces(true)
          }else
          {
            setOpenError(true)
          }
         

          const PedidoFilters = {
            PedidoId : 0,
             ProveedorId : 0
         }

         getPostsPedido(PedidoFilters)
            .then((res) => {
              setloading(false)
              const data = res.data

              const FiltersPermisos = {
                PerfilId : PerfilId,
                OpcionId : 0,
                OpcionUrl : '/CatalogoPedidos'
              }
              
              const PermisosMenu =  GetPermisosAccionPantallaPerfil(FiltersPermisos)
              .then((response) => {
        
                return {response}
        
              })
        
              var getPermisos = Promise.resolve(PermisosMenu);
              getPermisos.then(function(value) {
        
                const {SDTPerfilUsuarioOpcion} =  value.response.data
                const {Accion} = SDTPerfilUsuarioOpcion
          
        
                const DataWithMenu = data.map(Response => {
                  const {PedidoId, PedidoEstatus} = Response;
            
                  
                  //Se arman las opciones que tendr el men delmaterial table
            
                  let OpcionesMenuMaterialTable = []
                  // var {permisoModificarPedido, permisoRecibirPedido} = false
        
                  // Accion.map((accion) => {
                  //   const {AccionId, AccionAcceso} = accion
        
                  //   switch (AccionId) {
                  //     case "GENERAPEDIDO":
                  //      setPermisoGENERAPEDIDO(AccionAcceso)
                  //      break;
        
                  //    case "MODIFICAPEDIDO":
                  //     permisoModificarPedido = AccionAcceso
                      
                  //      break;
        
                  //    case "PEDIDOAUTOMATICO":
                  //      setPermisoPEDIDOAUTOMATICO(AccionAcceso)
                  //      break;
        
                  //    case "RECIBIRPEDIDO":
                  //     permisoRecibirPedido = AccionAcceso 
                  //     break;
                    
                  //    default:
                  //      break;
                  //   }
                   
                  // })
                  
        
        
                    OpcionesMenuMaterialTable = [
                      {
                        pathname : "/ViewPedido",
                        state : [{
                          Mode : "DSP",
                          CampoId : 'PedidoId',
                          id : PedidoId,
                        }],
              
                        TituloOpcion : "Visualizar"
                        },
                    ]
                    if ((PedidoEstatus === 2 || PedidoEstatus === 1) && PermisoMODIFICAPEDIDO) {
                      let datos = [...OpcionesMenuMaterialTable,
                      {
                        pathname : "/UpdatePedido",
                         state : [{
                           Mode : "UPD",
                           CampoId : 'PedidoId',
                           id : PedidoId,
                         }],
              
                         TituloOpcion : "Modificar"
                      }]
            
                      OpcionesMenuMaterialTable = datos
                    }
            
                    if (PedidoEstatus === 3 && PermisoRECIBIRPEDIDO) {
                      let datos = [...OpcionesMenuMaterialTable,
                        {
                          pathname : "/RecibirProductosPedido",
                           state : [{
                             Mode : "UPD",
                             CampoId : 'PedidoId',
                             id : PedidoId,
                           }],
                
                           TituloOpcion : "Recibir productos"
                        }]
              
                        OpcionesMenuMaterialTable = datos
                      
                    }
                  
                    return{...Response, DataWithMenu: <MenuPopupState OpcionesMenuMaterialTable= {OpcionesMenuMaterialTable}> </MenuPopupState>}
                 
                  
                })
        
        
                const DataWithStatus = DataWithMenu.map(Response => {
        
                  return{...Response, Status :  <div className={classes.root}>
                    {Response.PedidoEstatus == 1 && <Chip label="Abierto" style = {{backgroundColor : "rgb(243, 182, 0)", color:"white"}}/>}
                    {Response.PedidoEstatus == 2 && <Chip label="Autorizado" style = {{backgroundColor : "rgb(52, 152, 219)", color:"white"}}/>}
                    {Response.PedidoEstatus == 3 && <Chip label="Solicitado" style = {{backgroundColor : "rgb(249, 128, 102)", color:"white"}}/>}
                    {Response.PedidoEstatus == 4 && <Chip label="Recibido" style = {{backgroundColor : "rgb(46, 204, 113)", color:"white"}}/>}
                    {Response.PedidoEstatus == 5 && <Chip label="Cancelado" style = {{backgroundColor : "rgb(255,0,0)", color:"white"}}/>}
                    </div>}
                })
            
            
                 setdataMaterialTable(DataWithStatus)
            
              });
             
  
            });


         }).catch(reason => {
           console.log("la razon de error: " + reason);
         })
       })

    }

    function LoadingGuardado()
    {
      return(
        <div style = {{position : "absolute", backgroundColor : "white", width : "100%", zIndex : 2, textAlign : "center", padding : "7.1% 0"}}>
         <div style = {{display : "flex", marginLeft : "30%"}}>
          <p style = {{padding : "50px 0"}}>Generando</p> 
           <Loader
         type="Puff"
         color="#00BFFF"
         height={100}
         width={100}
 
      /> 
         </div>
         
        </div>
      );
    }

    return(
        <div>
            <MaterialTable
            title={
                <>Pedidos
                {
                PermisoGENERAPEDIDO && 
                <ButtonNew
                UrlButton = {"/NuevoPedido"}
                TittleButton = {" Nuevo Pedido"}
                Mode = "INS"
                />
                }
                
                {
                  PermisoPEDIDOAUTOMATICO &&
                  <Button
                    variant="outlined"
                    color="primary"
                    startIcon={<BrightnessAutoIcon></BrightnessAutoIcon>}
                    className={classes.ButtonPedidoAutomatico}
                    onClick = {GenerarPedidoAutomatico}
                   
                   >Generar pedido automtico</Button>
                }
                
               </>
            }
            columns = {[
                { title: 'NO. Pedido', field: 'PedidoId'},
                { title: 'Fecha', field: 'PedidoFechaHora', type: 'datetime',
                headerStyle: {
                  fontSize: 20
                  }, },
                { title: 'Estatus', field: 'Status' },
                
                { title: '', field: 'DataWithMenu',
                cellStyle: {
                  width: '1%',
               
                  },
                },
               
              ]}

            data= {dataMaterialTable}


            options={{
                // paging:false,
                  
                 headerStyle: {
                 backgroundColor: '#01579b',
                 color: '#FFF'
                 },
                 rowStyle: {
                 fontSize:16,
                 height:10,
                 width:20,
                },
                cellStyle: {
                 width: 200,
                 
               },
            }}
            localization = {{


              toolbar: {
                searchPlaceholder: 'Buscar pedido',
               
              },
      
              pagination: {
                labelDisplayedRows: '{from}-{to} de {count}',
                labelRowsSelect: 'Pedidos por pgina',
            },
            }}
            />

        <Dialog
            open={openPedidoAutomatico}
            onClose={handleClosePedidoAutomatico}
            scroll={scroll}
            aria-labelledby="scroll-dialog-title"
            aria-describedby="scroll-dialog-description"
            fullWidth = {true}
        
        >
            <div>
              {loading ?<LoadingGuardado/> :  ''}
            </div>

            <DialogTitle id="scroll-dialog-title"
            style = {{backgroundColor:"#fd9610", color:"#ffffff"}}
             >
              Seleccionar Almacn donde se generar el pedido

            <IconButton onClick={handleClosePedidoAutomatico} style = {{float: "right"}}>
              <CloseIcon />
            </IconButton>
            
            </DialogTitle>
            <DialogContent dividers={scroll === 'paper'}>

            
                <DialogContentText
                    id="scroll-dialog-description"
                    ref={descriptionElementRef}
                    tabIndex={-1}
                >
                
                  <div style = {{width:"95%"}}>
                      <ComboBoxAlmacen parentCallback = {callbackAlmacenFunction}/>
                  </div>

                

                </DialogContentText>

            </DialogContent>

           

            <Button
              variant="contained"
              color="primary"
              onClick = {AceptarGenerarPedidoAutomatico}
            >Generar</Button>


        </Dialog>

          {/* Seccion de alertas */}
          <div className = "AlertDiv">
            {/* Alerta de succes  */}

            <Collapse in={openSucces}>

            <Alert severity="success"
               action={
                 <IconButton
                 aria-label="close"
                 color="inherit"
                 size="small"
                 onClick={() => {
                 setOpenSucces(false);
                 }}
                 >
                 <CloseIcon fontSize="inherit" />
                 </IconButton>
               }
             >
            
            <AlertTitle><b> Aviso </b></AlertTitle>
               {MensajeRest}

              {PermisoMODIFICAPEDIDO &&
                <Link 
                to ={{
                pathname:"/UpdatePedido",
                state: {
                  Mode : "UPD",
                  id : PedidoIdGenerado
                }
                }}>
                        
                <br/>Modificar
             </Link>
              }
               
               
            </Alert>
            
            </Collapse>

             {/* Alerta de error  */}
          <Collapse in={openError}>
            <Alert severity="error"
              action={
              <IconButton
                aria-label="close"
                color="inherit"
                size="small"
                onClick={() => {
                  setOpenError(false);
                }
                }
                >
                <CloseIcon fontSize="inherit" />
              </IconButton>
              }
              >
              <AlertTitle><b> Error </b></AlertTitle>
                {MensajeRest}
              </Alert>

            </Collapse>

          </div>
            
        </div>
    )
});

export default MaterialTablePedido;