import MaterialTable from 'material-table';
import React, {Component} from 'react';
import TextField from "@material-ui/core/TextField";


const MaterialTableProductoHistorico = React.memo((props) => {
    const {posts} = props;
    const {ProductoCodigoBarra} = props;

    return(
                     <MaterialTable
                     data= {posts}
                     title= {`Cambios del producto  ${ProductoCodigoBarra}`}//"Histrico" + ProductoCodigoBarra
                     variant="outlined"
                     color="primary"
                     columns = {[
                         {title: '#', field: "ProductoHistoricoId",
                         cellStyle: {
                             width: '5%',
                           
                           }, },

                          {title: 'Comentarios', field: "ProductoHistoricoComentarios",
                           cellStyle: {
                               width: '80%',
                             
                             }, },
                           
                        
                         {title: 'Usuario', field: "UsuarioId",
                           cellStyle: {
                               width: '80%',
                             
                             }, },
                        
                         {title: 'Fecha', field: "ProductoHistoricoFecha",
                             cellStyle: {
                                 width: '80%',
                               
                               }, 
                               type: 'date'
                        },
                        
                        {title: 'Hora', field: "ProductoHistoricoHora",
                             cellStyle: {
                               width: '80%',
                                 
                                 }, 
                                 type: 'time'
                        }
                     ]}

                     onRowClick={(event, rowData, togglePanel) => togglePanel()}

                     detailPanel = {rowData => {
                        const dataDetail = rowData.Campos
                         return(
                            <MaterialTable
                            title="Histrico del producto"
                            columns = {[
                                {title: 'Campo', field: "CampoNombre"},
                                {title: 'Valor anterior', field: "CampoValorAnterior"},
                                {title: 'Valor nuevo', field: "CampoValorNuevo"}

                            ]}
                            data= {dataDetail}
                            options= {{
                                toolbar: false,
                                searchable: true,
                                
                                }}
                            
                            
                            >
                            </MaterialTable>

                         )
                     }}
                     
                     options={{
                      
                        headerStyle: {
                        backgroundColor: '#4FC3A1',
                        color: '#ffffff',
                        padding: 8,
                        position: 'sticky', 
                        top: 0
                        },
                        rowStyle: {
                        fontSize:16,
                        height:10,
                        width:20,
                       },
                       cellStyle: {
                        width: 200,
                        
                      },
                      
                   }}
                   
                     >
                         
                     </MaterialTable>
                 )

    
})



export default MaterialTableProductoHistorico


class FilterInnerComponent extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        filterType: "contains",
        filterValue: ""
      };
    }
  
    changeFilterType(event) {
      const filterType = event.target.value;
      const newState = {
        ...this.state,
        filterType
      };
      // Update local state
      this.setState(newState);
      // Fire the callback to alert React-Table of the new filter
      this.props.onChange(newState);
    }
  
    changeFilterValue(event) {
      const filterValue = event.target.value;
      const newState = {
        ...this.state,
        filterValue
      };
      // Update local state
      this.setState(newState);
      // Fire the callback to alert React-Table of the new filter
      this.props.onChange(newState);
    }
  
    render() {
      return (
        <div className="filter">
         
          <TextField
            onChange={evt => this.changeFilterValue(evt)}
            style={{
              width: "100%",
              height: "40px",
              float: "left",
              fontSize: "12px"
            }}
            value={this.state.filterValue}
          />
        </div>
      );
    }
  }
  
  function filterMethod(filter, row) {
    // Pivoted rows won't contain the column.
    //  If that's the case, we set the to true (allowing us to only filter on the current column)
    const rowValue = row[filter.id];
    if (!rowValue) {
      return true;
    }
  
    const filterValue = filter.value.filterValue || "";
    const filterType = filter.value.filterType;
    switch (filterType) {
      case "contains":
        return rowValue.indexOf(filterValue) > -1;
      case "starts-with":
        return rowValue.startsWith(filterValue);
      case "ends-with":
        return rowValue.endsWith(filterValue);
      case "matches":
        return rowValue === filterValue;
      case "greater-than":
        return rowValue > filterValue;
      case "less-than":
        return rowValue < filterValue;
      default:
        return true;
    }
}