import axios from 'axios';
import ipInfoLab from './../containers/Functions/GetIpInfolab';

export function getPostsProveedor(ProveedorFilters) {

  const {ProveedorId, ProveedorNombre, ProductoCodigoBarra} = ProveedorFilters
  

  const urlRest =   ipInfoLab + '/rest/CatalogoProveedor?ProveedorId=' + ProveedorId + '&ProveedorNombre=' + ProveedorNombre + '&ProductoCodigoBarra=' + ProductoCodigoBarra
  axios.delete(urlRest)
  return axios.get(urlRest);

}