import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTableClasificacion from './../components/ViewRegistros/MaterialTableClasificacion'
import { getPosts } from './../utils/RestClasificacion';


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoClasificacion extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }

  async  componentDidMount() {
        
    const ClasificacionFilters = {
      ProdClasificacionIductoId : 0,
      ClasificacionNombre : "",
      ClasificacionOrden : 0,
    }

    await getPosts(ClasificacionFilters)
    .then((res) => {
        console.log(res)
        this.setState({
            posts: res.data,
            loading : false,
        });

    });
};

render() {

  const {posts} = this.state; //Se recupera el posts de lo recibido del rest
  
  return (

    
    <Container>
     <MaterialTableClasificacion
    
      posts = {posts}
     
     />
   </Container>
  
  );
}

}
export default CatalogoClasificacion;