import React, { useEffect, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import styled from 'styled-components';
import ipInfoLab from '../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import Collapse from '@material-ui/core/Collapse';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import { getPosts } from './../../utils/RestClasificacion';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';


const useStyles = makeStyles((theme) => (
  {
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '100%',
      },
    },
  }));

const Container = styled.div`
margin: 1%;
width:100%;
`;


const CRUDCLASIFICACION = (props) => {
  
  const {ClasificacionId, Mode} = props.location.state
  
    const classes = useStyles();
    const [valueClasificacion, setValueClasificacion] = useState(''); //UseState recupera el valor introducido
    const [valueClasificacionOrden, setValueClasificacionOrden] = useState(0);
    const [ValueMensajeRest, setMensajeRest] = useState('');
    const [ValueExisteError, setExisteError] = useState(0);
    const [open, setOpen] = React.useState(false);
    const [DisabledCampos, setDisabledCampos] = React.useState(false);//si se entra en modo DSP no se podr modificar el registro
    
    useEffect(() => {
      if(Mode == "UPD" || Mode == "DSP")
      {

        if (Mode === 'DSP') {
          setDisabledCampos(true)
        }

        const ClasificacionFilters = {
          ClasificacionId : ClasificacionId,
          ClasificacionNombre : "",
          ClasificacionOrden : 0,
        }

        getPosts(ClasificacionFilters)
        .then((res) => {
          const data = res.data

          data.map(post => {
            const {ClasificacionNombre, ClasificacionOrden} = post
            setValueClasificacion(ClasificacionNombre)
            setValueClasificacionOrden(ClasificacionOrden)
          })
        })
      }

    },[false])


    //Por cada cambio en el textfield, se va guardando lo que el usuario va introduciendo en las funciones get
    function  getClasificacionValue(e){
        const ClasificacionValue = e.target.value;
        setValueClasificacion(ClasificacionValue)
    }

    function getClasificacionOrdenValue(e){
      setValueClasificacionOrden(e.target.value)
    }

    function SaveClasificacion()
    {
      const DataClasificacion = 
      {
        Modo : Mode,
        ClasificacionId : ClasificacionId,
        ClasificacionNombre :  valueClasificacion,
        ClasificacionOrden : valueClasificacionOrden,
      }

        const GuardarClasificacion = new Promise((resolve, reject) => {

          const urlRest = ipInfoLab + '/rest/GuardarClasificacion'
              
            axios.post(urlRest, DataClasificacion).then(response => {
              
              const MensajeRest = response.data.Mensaje
              const ExisteError = response.data.ExisteError
              setExisteError(ExisteError)
              setMensajeRest(MensajeRest)
                          
              if(ExisteError == 0)
              {
                           
                setTimeout(function(){ window.history.back(); }, 3000);

              }else
              {
                  setTimeout(function(){setOpen(false);; }, 6000);
                            
              }
                          
              }).catch(reason => {
                    console.log("la razon de error: " + reason);
              })
        });

      setOpen(true);

    }

    function ShowMessage()
    {
      if(ValueExisteError == 1)
      {

      
        return(
          <Collapse in={open}>
            <Alert severity="error"
              action={
                <IconButton
                  aria-label="close"
                  color="inherit"
                  size="small"
                  onClick={() => {
                    setOpen(false);
                  }}
                >
                  <CloseIcon fontSize="inherit" />
                </IconButton>
              }
            >
            <AlertTitle><b> Error </b></AlertTitle>
              {ValueMensajeRest}
            </Alert>
      
          </Collapse>

        )

      }
      else
      {
      
        return(
          <Collapse in={open}>
            <Alert severity="success"
              action={
                <IconButton
                  aria-label="close"
                  color="inherit"
                  size="small"
                  onClick={() => 
                    {
                      setOpen(false);
                    }
                  }
                >
                <CloseIcon fontSize="inherit" />
                </IconButton>
                }
            >
                <AlertTitle><b> Aviso </b></AlertTitle>
                  {ValueMensajeRest}
                </Alert>
        
          </Collapse>
      )
    }
    }

    return (
     <Container>
        {Mode == "INS" &&<h3>Nueva clasificacin</h3>}
        {Mode == "UPD" &&<h3>Modificar clasificacin</h3>}
        {Mode == "DSP" &&<h3>Visualizar clasificacin</h3>}
       

            <div className = "cardNewRegistro">
                <div className = "jss1 jss5 jss2">
                <form className={classes.root} noValidate autoComplete="off">
                    <TextField disabled  = {DisabledCampos} id="Clasificacion" label="Clasificacion" variant="outlined" onChange={getClasificacionValue} value = {valueClasificacion}/> 
                
                
                    <FormControl variant="outlined" className={classes.formControl}>
                      <InputLabel htmlFor="outlined-age-native-simple">NO.</InputLabel>
                      <Select
                      native
                      value={valueClasificacionOrden}
                      onChange={getClasificacionOrdenValue}
                      label="Clasificaion"
                      disabled  = {DisabledCampos}
                      >
                      <option aria-label="None" value="" />
                      <option value={1}>1</option>
                      <option value={2}>2</option>
                      <option value={3}>3</option>
                      <option value={4}>4</option>
                      <option value={5}>5</option>
                      </Select>
                    </FormControl>

                </form>


                <button  disabled = {DisabledCampos} type="button" className = {!DisabledCampos ? "btn btn-green-light btn-pill"  : "btn btn-gray-light btn-pill "} onClick = {SaveClasificacion} >Guardar</button>
                
                <div className = "AlertDiv">

                  <ShowMessage/>
                </div>
                
                </div>
            </div>
        
    </Container>  
    );
}


export default CRUDCLASIFICACION;
