import React, {useState, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import axios from 'axios';
import ipInfoLab from './../../containers/Functions/GetIpInfolab';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import {getPostsPerfil} from './../../utils/RestPerfil'
import Table from "./../Table/Table.js";
import Card from "./../Card/Card.js";
import CardHeader from "./../Card/CardHeader.js";
import CardBody from "./../Card/CardBody.js";
import MaterialTable from 'material-table';
import Checkbox from '@material-ui/core/Checkbox';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },

  button: {
    display: 'block',
    marginTop: theme.spacing(2),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  
}));

export default function NestedList() {
  const classes = useStyles();
  const [OpcionesMenu, setOpcionesMenu] = useState([]);
  const [chekedAutorizar, setchekedAutorizar] = useState(false)


  //Variable donde se guarda los permisos
  const [SDTPerfilUsuarioOpcion, setSDTPerfilUsuarioOpcion] = useState()
  const [AccionesOpcion, setAccionesOpcion] = useState([])

  //Combo box perfil
  const [openCombo, setOpenCombo] = React.useState(false);
  const [Perfiles, setPerfiles] = useState([])

  //variables para indicar el perfil y la opcion seleccionada
  const [OpcionIdSelected, setOpcionIdSelected] = useState(0);
  const [PerfilIdSelected, setPerfilIdSelected] = useState(0);

  //En la variable HeaderAbierto se guarda el submenu que el usuario abri para que se mantenga abierto al momento de consultar los servisios rest(Sin esto se cierra)
  const [HeaderAbierto, setHeaderAbierto] = useState(0)



  useEffect(() => {

    // Se obtienen las opciones del menu
    const urlPost = ipInfoLab + '/rest/OpcionesMenu'
    axios.post(urlPost)
    .then((response) => {
      const {SDTOpcionesMenu} = response.data

      const OpenMenu = SDTOpcionesMenu.map(Response => {
        return{...Response, OpenMenu : false}
      })

      setOpcionesMenu(OpenMenu)
    })

    // Se obtienen los perfiles

    const PerfilFilters = {
      PerfilId : 0,
      PerfilNombre : "",
    }

    getPostsPerfil(PerfilFilters)
    .then((res) => {
      const {SDTPerfiles} = res.data
      setPerfiles(SDTPerfiles)
    })


    
   
  },[SDTPerfilUsuarioOpcion])



  //abrir combo
  const handleChangeCombo = (event) => {
    const PerfilSeleciconado = event.target.value
    setPerfilIdSelected(PerfilSeleciconado);

    const FiltersPermisos = {
      PerfilId : PerfilSeleciconado,
      OpcionId : OpcionIdSelected,
      OpcionUrl : ''
    }

    const urlPost = ipInfoLab + '/rest/ConsultaPermisosPerfil'
    axios.post(urlPost, FiltersPermisos)
    .then((response) => {
      const {data} = response
      const {OpcionAcceso} = data.SDTPerfilUsuarioOpcion

      const {SDTPerfilUsuarioOpcion} = data

      setSDTPerfilUsuarioOpcion(SDTPerfilUsuarioOpcion)

      var dataAcciones = []

    
    
         SDTPerfilUsuarioOpcion.Accion.map((accion, i) => {
          dataAcciones[i] = [accion.AccionId]
          //  return{...response}
         })
      
     
        setAccionesOpcion(dataAcciones)

        setchekedAutorizar(OpcionAcceso)

    })
    .catch(reason => {
      console.log("la razon de error: " + reason);
    })
  };

  const handleCloseCombo = () => {
    setOpenCombo(false);
  };

  const handleOpenCombo = () => {
    setOpenCombo(true);
  };

  const handleChangeAcceso = (event) => {
    setchekedAutorizar(!chekedAutorizar)


    const datosPermisos = SDTPerfilUsuarioOpcion

    datosPermisos.OpcionAcceso = !datosPermisos.OpcionAcceso

    

    setSDTPerfilUsuarioOpcion(datosPermisos)
    CambiarPermisos()
    
  };

  const CambiarPermisos = () => {
    
    const DataPermisos = {
      SDTPerfilUsuarioOpcion : SDTPerfilUsuarioOpcion
    }

    const urlPost = ipInfoLab + '/rest/GuardarPermisosMenu'
    axios.post(urlPost, DataPermisos)
    .then((response) => {
     


    })
    .catch(reason => {
      console.log("la razon de error: " + reason);
    })
  }

 
  //Evento para dar acceso a la accin de la ocin

  const handleChangeAccion = (e) => {
    const id = e.target.id

    const datosPermisos = SDTPerfilUsuarioOpcion

    datosPermisos.Accion.map((response) => {
      if(response.AccionId === id)
      {
        response.AccionAcceso = !response.AccionAcceso
      }
    })

    setSDTPerfilUsuarioOpcion(datosPermisos)

    setchekedAutorizar(!chekedAutorizar)

    CambiarPermisos()

  }

  
  const MaterialTableAcciones = () => {
    return(
    <MaterialTable
    title = ""
    columns = {[
      {title : '', field : 'AccionAcceso', 
      cellStyle: {
        width: '1%',
        padding : 0
       
      },
      render : rowData => ( 
      <Checkbox
      id = {rowData.AccionId}
      checked={rowData.AccionAcceso}
      onChange={handleChangeAccion}
      inputProps={{ 'aria-label': 'primary checkbox' }}
     
    />
      )
    },
      {
        
        title : '', field : 'AccionDescripcion',
        cellStyle: {
          
          width: '100%',
          padding : 0
        },
      }
    ]}

    data = {SDTPerfilUsuarioOpcion === undefined ? [] : SDTPerfilUsuarioOpcion.Accion}

    options= {{
     
      toolbar: false,
      paging: false,
      
      }}

    />
    )
  }
  return (

    <div style = {{width : "100%", display : "flex"}}>
     

    
    <List
    style = {{position : "fixed", height : "100%"}}
      component="nav"
      aria-labelledby="nested-list-subheader"

      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          Opciones del men
        </ListSubheader>

        
      }
      className={classes.root}
    >

      {

        OpcionesMenu.map((menu) => {

          let MenuList = "";

            if(menu.OpcionTipo === 'Simple') {
              MenuList = 

              <ListItem button onClick={
                () => {
                      setOpcionIdSelected(menu.OpcionId)
                      
                       const FiltersPermisos = {
                         PerfilId : PerfilIdSelected,
                         OpcionId : menu.OpcionId,
                         OpcionUrl : ''
                       }
            
                       const urlPost = ipInfoLab + '/rest/ConsultaPermisosPerfil'
                       axios.post(urlPost, FiltersPermisos)
                       .then((response) => {
                          const {data} = response
                          const {OpcionAcceso} = data.SDTPerfilUsuarioOpcion

                          const {SDTPerfilUsuarioOpcion} = data
                          setSDTPerfilUsuarioOpcion(SDTPerfilUsuarioOpcion)
                          setchekedAutorizar(OpcionAcceso)

               
                  
                       })
                       .catch(reason => {
                         console.log("la razon de error: " + reason);
                       })
                  }
                }
                
                style = {OpcionIdSelected === menu.OpcionId ? {backgroundColor : "#3498DB", color : "white"} : {}}>

                <ListItemIcon>
                  <i className = {menu.OpcionIcono} style = {OpcionIdSelected === menu.OpcionId ? { color : "white"} : {}}></i>
                </ListItemIcon>
                <ListItemText primary={menu.OpcionNombre} />
              </ListItem>
            }else if(menu.OpcionTipo === 'Header')
            {
              MenuList = 
              <div>
                  <ListItem button onClick={() => 
                  {
                    const d = OpcionesMenu.map(Response => {

                    let OpenMenu = false
                    if(menu.OpcionId === Response.OpcionId)
                    {
                      OpenMenu = !Response.OpenMenu
                    }
                        return{...Response, OpenMenu : OpenMenu}
                      })
        
                      setOpcionesMenu(d)
                      setHeaderAbierto(menu.OpcionId)
                  }
                }

                  
                  >
                     
                    <ListItemIcon>
                      <i className = {menu.OpcionIcono}></i>
                    </ListItemIcon>
                    <ListItemText primary={menu.OpcionNombre} />
                      {menu.OpenMenu ? <ExpandLess /> : <ExpandMore />}
                  </ListItem>

                {
                  menu.SubMenus.map((submenu) => {
                    return(

                    
                      <Collapse 
                      in={HeaderAbierto === menu.OpcionId ? true : false}//{menu.OpenMenu} 
                      timeout="auto" unmountOnExit>

                        <List component="div" disablePadding>
                          <ListItem button 
                            onClick={
                              () => {
                                setOpcionIdSelected(submenu.SubMenuId)
                      
                                const FiltersPermisos = {
                                  PerfilId : PerfilIdSelected,
                                  OpcionId : submenu.SubMenuId,
                                  OpcionUrl : ''
                                }

                                const urlPost = ipInfoLab + '/rest/ConsultaPermisosPerfil'
                                axios.post(urlPost, FiltersPermisos)
                                 .then((response) => {
                                   const {data} = response
                                   const {OpcionAcceso} = data.SDTPerfilUsuarioOpcion

                                   const {SDTPerfilUsuarioOpcion} = data
                                   console.log(SDTPerfilUsuarioOpcion)
                                   setSDTPerfilUsuarioOpcion(SDTPerfilUsuarioOpcion)
                                   setchekedAutorizar(OpcionAcceso)
                                 })
                                 .catch(reason => {
                                   console.log("la razon de error: " + reason);
                                 })
                              }
                            }
                            className={classes.nested}
                            style = {OpcionIdSelected === submenu.SubMenuId ? {backgroundColor : "#3498DB", color : "white"} : {}}
                            >

                            <ListItemIcon>
                              <StarBorder />
                            </ListItemIcon>
                            <ListItemText primary={submenu.SubMenuNombre} />
                          </ListItem>
                        </List>

                      </Collapse>
                    )
                  })
                }
                
              </div>
                
            }

          return MenuList;
        })
      
      }

      

     
    </List>

    <div style = {{marginLeft : "380px", width : '100%'}}>
    

    
   


    {/* Combo de perfil */}

    <FormControl className={classes.formControl}>
        <InputLabel id="demo-controlled-open-select-label">Perfil</InputLabel>
        <Select
          labelId="demo-controlled-open-select-label"
          id="demo-controlled-open-select"
          open={openCombo}
          onClose={handleCloseCombo}
          onOpen={handleOpenCombo}
          value={PerfilIdSelected}
          onChange={handleChangeCombo}
        >

          {
            Perfiles.map((perfil) => {
              return(
                <MenuItem value={perfil.PerfilId}>{perfil.PerfilNombre}</MenuItem>
              )
              
            })
          }
        </Select>
    
      </FormControl>


       {/* swtch para autorizar objeto menu  */}
        <FormGroup row>
      
        <FormControlLabel
          control={
            <Switch
              // checked={chekedAutorizar}
              
              onChange={handleChangeAcceso}
              name="chekedAutorizar"
              color="primary"
              checked = {SDTPerfilUsuarioOpcion === undefined ?  false : SDTPerfilUsuarioOpcion.OpcionAcceso }
            />
          }
          label="Autorizar"
        />
     
        </FormGroup>
        <Card>
            <CardHeader color="warning" style = {{height : "100px", padding : "10px"}}>
              <h4 className={classes.cardTitleWhite}>Permisos</h4>
              <p className={classes.cardCategoryWhite}>
                Seleccione la casilla para asignar permiso
              </p>
            </CardHeader>
            <CardBody>
             <MaterialTableAcciones/>
            </CardBody>
          </Card>
      </div>
    </div>

  );
}