import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import {Link} from 'react-router-dom';

const MenuPopupState = ({OpcionesMenuMaterialTable}) => {
 
  return (
    <PopupState variant="popover" popupId="demo-popup-menu">
      {(popupState) => (
        <React.Fragment>
          <Button variant="" color="" {...bindTrigger(popupState)}>
            <MoreVertIcon/>
          </Button>
          <Menu {...bindMenu(popupState)}>

            {OpcionesMenuMaterialTable.map((opcionmenu) => {
              return(
              <Link 
              to = {{
                pathname : opcionmenu.pathname,

                
                state : {
                  Mode :opcionmenu.state[0].Mode,
                  id : opcionmenu.state[0].id,
                }
              }}
              >
                 <MenuItem onClick={popupState.close}>{opcionmenu.TituloOpcion}</MenuItem>
              </Link>
              )
            })}
        
          



          </Menu>
          
          
          
        </React.Fragment>
      )}
    </PopupState>
  );
}

export default MenuPopupState;