import React, {useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import {getPostsLoginRest} from '../../utils/RestLogin';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import PropTypes from 'prop-types';

function Copyright() {
    return (
      <Typography variant="body2" color="textSecondary" align="center">
        {'Copyright  '}
        <Link color="inherit" href="https://material-ui.com/">
          DICIPA CORP
        </Link>{' '}
        {new Date().getFullYear()}
        {'.'}
      </Typography>
    );
  }
  
  const useStyles = makeStyles((theme) => ({
    root: {
      height: '100vh',
    },
    image: {
      backgroundImage: 'url(https://bioalimentar.com/newpage/wp-content/uploads/2018/12/LaboratorioHeader.jpg?id=4635&45dd30&45dd30)',
      backgroundRepeat: 'no-repeat',
      backgroundColor:
        theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    },
    paper: {
      margin: theme.spacing(8, 4),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
  }));
  
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

  export default function SignInSide({ setToken, setTokenSdtUsuario }) {
    const classes = useStyles();
    const [Usuario, setUsuario] = useState('');
    const [Contrasenia, setContrasenia] = useState('');
    const [open, setOpen] = React.useState(false);
    const [ErrorLogin, setErrorLogin] = useState(0);
    const [MensajeLogin, setMensajeLogin] = useState('');
    // const [SdtUsuario, setTokenSdtUsuario] = useState({})


    function GetUser(e){
        const usuario = e.target.value
        setUsuario(usuario)
    }
  
    function getContrasenia(e) {
        const contrasenia = e.target.value
        setContrasenia(contrasenia)
    }

    function IniciarSesion(){
        const LoginFilters = {
            UsuarioId : Usuario,
            UsuarioPassword : Contrasenia
        }
  
        getPostsLoginRest(LoginFilters)
        .then((response) => {
            const {MensajeLogin, ErrorLogin, sessionid, SdtUsuario} = response.data
       
            if(ErrorLogin != 0)
            {
                setMensajeLogin(MensajeLogin)
                setOpen(true);
            }
            else
            {
                setToken(sessionid)
                setTokenSdtUsuario(SdtUsuario)
            }
        })
        .catch(err => {
          console.log("error : " + err)
        })

       
    }


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
        setMensajeLogin('')
        setErrorLogin(0)
        setOpen(false);
      };

    return (
      <Grid container component="main" className={classes.root}>
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <div className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
                Iniciar sesin
            </Typography>
            <form className={classes.form} noValidate>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="email"
                label="Usuario"
                name="email"
                value = {Usuario}
                onChange = {e => setUsuario(e.target.value)}//{GetUser}
                // autoComplete="email"
                autoFocus
              />
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="password"
                label="Contrasea"
                type="password"
                id="password"
                value = {Contrasenia}
                onChange = {e => setContrasenia(e.target.value)}//{getContrasenia}
                // autoComplete="current-password"
              />
              <FormControlLabel
                control={<Checkbox value="remember" color="primary" />}
                label="Ingresar con licencias infolab"
              />
              <Button
                // type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick = {IniciarSesion}
              >
                Ingresar
              </Button>
              <Grid container>
                <Grid item xs>
                  <Link href="#" variant="body2">
                    Olvid la contrasea?
                  </Link>
                </Grid>
              </Grid>
              <Box mt={5}>
                <Copyright />
              </Box>
              
                <Box mt={5}>
                    <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                        <Alert onClose={handleClose} severity="error">
                           {MensajeLogin}
                        </Alert>
                    </Snackbar>
                </Box>
            </form>
          </div>
        </Grid>
      </Grid>
    );
  }

  SignInSide.propTypes = {
    setToken: PropTypes.func.isRequired,
    setTokenSdtUsuario : PropTypes.func.isRequired
  }