import axios from 'axios';
import ipInfoLab from './../containers/Functions/GetIpInfolab';

export function getPosts(ProductoFilters) {

  const{ProductoNombre, ProductoId, ProveedorId, ProductoCodigoBarra, AlmacenId} = ProductoFilters
  
  const urlRest = ipInfoLab + '/rest/Catalogoproducto?ProductoNombre=' + ProductoNombre + '&ProductoId=' + ProductoId + '&ProductoCodigoBarra=' + ProductoCodigoBarra + '&ProveedorId=' + ProveedorId + '&AlmacenId=' + AlmacenId
  axios.delete(urlRest)
  return axios.get(urlRest)
}