function App(){
  const { token, setToken, TokenSdtUsuario, setTokenSdtUsuario } = useToken();
  const [locale, setLocale] = useState('en');



  if(!token || !TokenSdtUsuario) {
    
    return <Login 
    setToken={setToken} 
    setTokenSdtUsuario = {setTokenSdtUsuario}
    />
  }
 
  return (
   
    <IntlProvider locale={locale}>
      <Layout 
      setLocale={setLocale} 
      // setToken = {setToken}
      TokenSdtUsuario = {TokenSdtUsuario}
     
      />
    </IntlProvider>
    
  );
}

export default App;