import React, { useState, useEffect } from 'react';
import { useIntl } from 'react-intl';
import {
  ProSidebar,
  Menu,
  MenuItem,
  SubMenu,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
} from 'react-pro-sidebar';
import { FaTachometerAlt, FaGem, FaList, FaGithub, FaRegLaughWink, FaHeart } from 'react-icons/fa';
import {Link} from 'react-router-dom';
import { FaBars } from 'react-icons/fa';
import { BiExit, BiHomeAlt, BiListUl, BiBox, BiBarcodeReader, BiUser } from "react-icons/bi";
import ipInfoLab from './../../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import useToken from './../../Login/useToken';

const Aside = ({ image, collapsed, rtl, toggled, handleToggleSidebar }) => {
  const intl = useIntl();

  const [showMenu, setShowMenu] =  useState(collapsed)
  const {TokenSdtUsuario} = useToken();
  const {UsuarioId, PerfilId} =  TokenSdtUsuario

  /*Se obtienen las opciones del menu que tiene permiso el perfil[FIN]*/

   function ShowHiddeMenu() {
    {showMenu ? setShowMenu(false) :  setShowMenu(true)}
    
   }

   function CerrarSesion() {
    localStorage.setItem('token', JSON.stringify(''))
   }

   const [OpcionesMenu, setOpcionesMenu] = useState([])

   const filterOpcionesMenuPerfil = {
    PerfilId : PerfilId
   }
     useEffect(() => {

       const urlPost = ipInfoLab + '/rest/OpcionesMenuPerfil'
       axios.post(urlPost, filterOpcionesMenuPerfil)
       .then((response) => {
        const {SDTOpcionesMenu} = response.data
       
        setOpcionesMenu(SDTOpcionesMenu)
        OpcionesMenu.map((response) => {
          
        })
       })
       
      
     },[false])
    

    //  setOpcionesMenu(Opciones)
     console.log({OpcionesMenu})

   const RenderMenuOption =() => {

       return(//!OpcionesMenu["menus"].length ? "" :
       //(
        <Menu iconShape="circle">
        {OpcionesMenu.map((menu) => {
            let liElementList = "";

            if (menu.OpcionTipo === 'Simple') {
              liElementList =
              <MenuItem
              icon={<i className = {menu.OpcionIcono}></i>}
              >

                <Link to = {menu.OpcionUrl}>
                  {menu.OpcionNombre}
                </Link>

              </MenuItem>
            }
            else if (menu.OpcionTipo === 'Header')
            {
              liElementList =

              <SubMenu
              // suffix={<span className="badge gray">5</span>}
              title={intl.formatMessage({ id: menu.OpcionNombre })}
              icon={<i className = {menu.OpcionIcono}></i>}
              >

                
                {menu.SubMenus.map((submenu) => {
                  return(<MenuItem><Link to = {submenu.SubMenurUrl}>{submenu.SubMenuNombre}</Link></MenuItem>)
                })}
              </SubMenu>
            } 

                return liElementList;
            })
        }
        </Menu>

      
   
     )
   }

  return (
    <ProSidebar
      image="https://bioalimentar.com/newpage/wp-content/uploads/2018/12/LaboratorioHeader.jpg?id=4635&45dd30&45dd30"
      rtl={rtl}
      collapsed={showMenu}
      toggled={toggled}
      breakPoint="md"
      onToggle={handleToggleSidebar}
    >
      <SidebarHeader>
        <div
          style={{
            padding: '24px',
            textTransform: 'uppercase',
            fontWeight: 'bold',
            fontSize: 14,
            letterSpacing: '1px',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}
        >
          {/* {intl.formatMessage({ id: 'DICIPA' })} */}
          <FaBars 
          onClick = {ShowHiddeMenu}/>
        </div>
      </SidebarHeader>

      <SidebarContent>
        {RenderMenuOption()}
      </SidebarContent>
        

      <SidebarFooter style={{ textAlign: 'center' }}>
        <div
          className="sidebar-btn-wrapper"
          style={{
            padding: '20px 24px',
          }}
        >

        <a 
        onClick={() => { window.location.href="/"; localStorage.setItem('token', JSON.stringify(''));}}
        className="sidebar-btn"
        rel="noopener noreferrer"
        >
          <BiExit/>
          <span> {intl.formatMessage({ id: 'Cerrar sesion' })}</span>
        </a>
        </div>

        
      </SidebarFooter>
    </ProSidebar>
  );
};

export default Aside;
