import React, { Component } from 'react';
import styled from 'styled-components';
import ButtonNew from './Buttons/ButtonNewRegistro';
import MaterialTableProducto from './../components/ViewRegistros/MaterialTableMotivoMovimiento'
import { getPosts } from './../utils/RestMotivoMovimiento';
import MaterialTableMotivoMovimiento from './../components/ViewRegistros/MaterialTableMotivoMovimiento';


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoMotivoMovimiento  extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }

  async  componentDidMount() {
        
    const MotivoMovimientoFilters = {
      MotivoMovimientoId : 0,
      MotivoMovimientoNombre : "",
      MotivoMovimientoTipo:""
    }

    await getPosts(MotivoMovimientoFilters)
    .then((res) => {
        console.log(res)
        this.setState({
            posts: res.data,
            loading : false,
        });

    });
  };

  render() {

    const {posts} = this.state; //Se recupera el posts de lo recibido del rest
    
    return (

      
      <Container>
       <MaterialTableMotivoMovimiento
      
        posts = {posts}
       
       />
     </Container>
    
    );
  }


}
export default CatalogoMotivoMovimiento;