import React, {useEffect, useRef, useState} from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import Paper from '@material-ui/core/Paper';
import {getPosts} from './../../../utils/RestProducto'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { green, purple } from '@material-ui/core/colors';
import zIndex from '@material-ui/core/styles/zIndex';
import InputField from './InputField'
import {useForm} from 'react-hook-form';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import { getPostsProveedor } from './../../../utils/RestProveedor';
import Collapse from '@material-ui/core/Collapse';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 4,
    },
    paper: {
      height: 200,
      width: 280,
    },
    control: {
      padding: theme.spacing(2),
    },

    container : {
        padding: "2px 16px"
    },

    TextNameProducto : {
        color: "gray",
        fontSize: "small"
    },

    DivCantidad : {
        display:"flex",
        width:"100%",
        backgroundColor:"#22c488",//"#dcfcdc",
        padding : "3px"
    },

    rootTextField : {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '5ch',
          },

    },

    button: {
        margin: theme.spacing(1),
        marginLeft : 20,
      },


      DetalleProducto : {
        padding:5,
        display:"flex",
      },

      DivDescripcionFiltro : {
        position: "-webkit-sticky;",
        position : "sticky",
        top: 100,
        backgroundColor: "#ecf6fd",
        // padding : "10px",
        zIndex: 1,
        display:"flex",
        fontSize: 12
      },

      DivFiltros : {
          position: "-webkit-sticky;",
          position : "sticky",
          top: 5,
          backgroundColor: "rgb(253, 150, 16)",
          padding : "10px",
          fontSize: "20px",
          zIndex: 1,
          display:"flex",
      
      },

      InputCantidad : {
        padding : "8px",
        width : 50
      },

      ButtonAgregarProducto : {

        backgroundColor :  "#CACFD2",
        border : "none",
        color : "#2C3E50",
        padding : "12px 16px",
        fontSize : "16px",
        cursor : "pointer",
        float : "right",
        borderRadius : 50,


        '&:hover': {
             backgroundColor: green[700],
           },
      },

      FormTextFieldFiltros : {
        padding : 10,
      }
  }));

  const ColorButton = withStyles((theme) => ({
    root: {
      color: "white",
      backgroundColor:"#54a449",
      '&:hover': {
        backgroundColor: green[700],
      },

      fontSize:10,

      height: 30,
      
      marginTop: 8,

      marginLeft :"35%",
    },
  }))(Button);



  const ButtonCarrito = withStyles((theme) => ({
    root: {
     
      color: "white",
      backgroundColor: "#15b765",
      '&:hover': {
        backgroundColor: "#0a86d4",
      },
      borderRadius:100,
      // padding:"10px",
      height:60,
      boxShadow: "0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19)",
      bottom:10,
      right:10,
      position:"fixed",
      zIndex:100
    },
  }))(Button);


  const ButtonBuscar = withStyles((theme) => ({
    root: {
     
      color: "white",
      backgroundColor: "#22c488",
      '&:hover': {
        backgroundColor: "#0a86d4",
      },
      marginTop : 20,
      boxShadow: "0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19)",
      height : 40
    },
  }))(Button);

function SeleccionarProductoPedido(props){

const [spacing, setSpacing] = React.useState(5);
const classes = useStyles();
const [Productos, setProductos] = React.useState([]);
const [refCantidad, setrefCantidad] = React.useState(React.createRef)
const [ItemProducto, setItemProducto] = React.useState(0)
const [ProductosSeleccionados, setProductosSeleccionados] = useState(props.ProductosSeleccionados)
const [TotalProductosCarrito, setTotalProductosCarrito] = useState(props.ProductosSeleccionados.length)
const [loading, setLoading] = useState(true)
const nameForm = useRef(null);
const { register, handleSubmit, errors } = useForm();
const [ValueProveedor, setValueProveedor] = useState(0)
const [PostsProveedores, setPostsProveedores] = useState([])
const [FiltroProveedor, setFiltroProveedor] = useState(0)
const [FiltroCodigoBarra, setFiltroCodigoBarra] = useState("");
const [FiltroProductoDescripcion, setFiltroProductoDescripcion] = useState("")
const [TextoFiltro, setTextoFiltro] = useState("");
const [ValorTextoComboProveedor, setValorTextoComboProveedor] = useState("ValorTextoComboProveedor")
const [openSucces, setOpenSucces] = React.useState(false);
const [openError, setOpenError] = React.useState(false);
const [ValueMensaje, setMensaje] = useState('');
const [AlmacenId, setAlmacenId] = useState(0)

useEffect(() => {
  setAlmacenId(props.AlmacenId)
})


useEffect(function () {
  setAlmacenId(props.AlmacenId)



  const CallProductos = () => {
    const ProductoFilters = {
      
                  ProductoId : 0,
                  ProductoNombre : FiltroProductoDescripcion,
                  ProductoCodigoBarra :FiltroCodigoBarra,
                  ProveedorId : FiltroProveedor,
                  AlmacenId: AlmacenId
                }

                getPosts(ProductoFilters)
                .then((res) => {
                  const data = res.data.map(Response => {
                    var cantidad = 0

                    ProductosSeleccionados.map(res => {
                                              if(res.ProductoId === Response.ProductoId)
                                              {
                                                cantidad = res.PedidoProductosCantidad
                                              }
                                               
                                            })

                                            return{...Response, PedidoProductosCantidad : cantidad}
                  })

                  setProductos(data)
                })
  }

  CallProductos()


  const ProveedorFilters = {
                  ProveedorId : 0,
                  ProveedorNombre: "",
                  ProductoCodigoBarra : "",
                  ProductoCodigoBarra: ""
                }
               
                getPostsProveedor(ProveedorFilters)
                .then((res) => {
                  setPostsProveedores(res.data)
               })
  
               .catch((err) => console.log(err));
}, [AlmacenId])


const onSubmit = (data, e) => {
  const ItemProducto = e.target.elements[0].name
  var ItemProductoAgregado = 0
  var Total = ProductosSeleccionados.length

  var ExisteProducto = false
  var MensajeAgrega = "" 
  
  ProductosSeleccionados.map((response, i) => {
    if(response.ProductoId ===  Productos[ItemProducto].ProductoId)
    {
      ExisteProducto = true
      ItemProductoAgregado = i
    }
  })

  if(data[ItemProducto] > 0)
  {

  
  if(ExisteProducto === true)
  {

    ProductosSeleccionados[ItemProductoAgregado].PedidoProductosCantidad = data[ItemProducto]

  }else
  {

  
    const d = [...ProductosSeleccionados, 
      {
      ProductoId : Productos[ItemProducto].ProductoId,
      ProductoCodigoBarra : Productos[ItemProducto].ProductoCodigoBarra,
      ProductoNombre : Productos[ItemProducto].ProductoNombre,
      ProveedorId : Productos[ItemProducto].ProveedorId,
      ProveedorNombre : Productos[ItemProducto].ProveedorNombre,
      ProductoUnidadMedidaNombreCompra : Productos[ItemProducto].ProductoUnidadMedidaNombreCompra,
      ProductoUnidadMedidaNombreConsumo : Productos[ItemProducto].ProductoUnidadMedidaNombreConsumo,
      PedidoProductosCantidad : data[ItemProducto],
      ProductoPrecioCompra : Productos[ItemProducto].ProductoPrecioCompra,
      ProductoPrecioTotal : (Productos[ItemProducto].ProductoPrecioCompra * data[ItemProducto]),
      CantidadExistente : Productos[ItemProducto].CantidadExistente,
      BackOrder : Productos[ItemProducto].BackOrder,
      }
    ];

    setProductosSeleccionados(d)
    Total = ProductosSeleccionados.length + 1

  }

  MensajeAgrega = "Producto " +  Productos[ItemProducto].ProductoCodigoBarra + " agregado"

  setOpenError(false)
  setOpenSucces(true)


  setTimeout(
    function()
    {
      setOpenSucces(false)
    }, 4000
  );

   
  }
  else
  {
    MensajeAgrega = "El producto no puede ir en Cero o negativo"
    setOpenSucces(false)
    setOpenError(true)

    setTimeout(
      function()
      {
        setOpenError(false)
      }, 3000
    );
    
  }

  
  
  
  setTotalProductosCarrito(Total)

  setMensaje(MensajeAgrega)
  

}

const MostrarCarrito = () => {

  props.parentCallBackShowProductos(false)
  props.parentCallbackProductosSeleccionados(ProductosSeleccionados)
}

const BuscarProductos = () => {

  const ProductoFilters = {
    ProductoId : 0,
    ProductoNombre : FiltroProductoDescripcion,
    ProductoCodigoBarra : FiltroCodigoBarra,
    ProveedorId : FiltroProveedor,
    AlmacenId: AlmacenId
  }
  
  getPosts(ProductoFilters)
  .then((res) => {
    const data = res.data.map(Response => {

      var cantidad = 1

      ProductosSeleccionados.map(res => {
        if(res.ProductoId === Response.ProductoId)
        {
          cantidad = res.PedidoProductosCantidad
        }
        
      })

        return{...Response, PedidoProductosCantidad : cantidad}
    })
    

    setProductos(data)

   
    var MensajeFiltro = ""

    if(FiltroProveedor > 0 || FiltroCodigoBarra != "" || FiltroProductoDescripcion != "")
    {

      MensajeFiltro = "Se muestran productos"

      MensajeFiltro += (FiltroProveedor > 0) ? " del Proveedor " + ValorTextoComboProveedor + " " : ""

      MensajeFiltro += (FiltroCodigoBarra != "") ? " con cdigo de barra " + FiltroCodigoBarra + " ": ""

      MensajeFiltro += (FiltroProductoDescripcion != "") ? " con Descripcin " + FiltroProductoDescripcion : ""
    }
    
    
    setTextoFiltro(MensajeFiltro)
    
  }
  )
}

 const ListaProveedores = () => {
  return PostsProveedores.map(post => {

    const { ProveedorNombre, ProveedorId } = post;

    return (
      <option value={ProveedorId}>{ProveedorNombre}</option>
    ); 

  })
 }


 const getfiltroProveedor = event => {
  const ProveedorValue = event.target.value
  setFiltroProveedor(ProveedorValue)



  let index = event.target.selectedIndex;
  setValorTextoComboProveedor(event.target.options[index].text)


}

const getFiltroCodigoBarra = event => {
  const CodigoBarra = event.target.value
  setFiltroCodigoBarra(CodigoBarra)
  
}

const getFiltroProductoDescripcion = event => {
  setFiltroProductoDescripcion(event.target.value)
}

  return (
    <div>

      
    <div className = {classes.DivFiltros}>
      
             <p>Filtros</p>
             <div style = {{width:"100%", display:"flex",}}>
                        <FormControl variant="outlined" className="" className = {classes.FormTextFieldFiltros} >
                        <InputLabel htmlFor="outlined-age-native-simple">Proveedor</InputLabel>
                        <Select
                        native
                        fullWidth={true}
                        value = {FiltroProveedor}
                        onChange={getfiltroProveedor}
                        label="Proveedor"
                        inputProps={{
                        name: 'Proveedor',
                        id: 'outlined-age-native-simple',
                        
                        }}
                        >
                     
                        <option value={0}>Todos</option>
                        <ListaProveedores/>
                        </Select>


                        </FormControl>


                      <form noValidate autoComplete="off" className = {classes.FormTextFieldFiltros}>
                        <TextField 
                        id="CodigoBarra" 
                        label="Cdigo de barra" 
                        variant="outlined" 
                         onChange={getFiltroCodigoBarra} 
                         value = {FiltroCodigoBarra} 
                        // fullWidth = {true}
                        /> 
                      </form> 


                      <form noValidate autoComplete="off" className = {classes.FormTextFieldFiltros}>
                        <TextField 
                        id="ProductoDescripcion" 
                        label="Producto" 
                        variant="outlined" 
                         onChange={getFiltroProductoDescripcion} 
                         value = {FiltroProductoDescripcion} 
                        // fullWidth = {true}
                        /> 
                      </form> 

                    </div>

                    
                    <ButtonBuscar variant="contained" color="primary" onClick = {BuscarProductos}>
                       Buscar
                    </ButtonBuscar>
          </div>

          {TextoFiltro != "" && (
            <div className = {classes.DivDescripcionFiltro}>

              <p>{TextoFiltro} </p>

            </div>
          )}

          

      <div style = {{paddingTop : 30}}>

      
      <Grid container className={classes.root} spacing={2}>
        <Grid item xs={12}>
          <Grid container justify="center" spacing={spacing}>
            {Productos.map((value, i) => (
              <Grid key={i} item>
                <Paper className={classes.paper} style = {{backgroundColor : "#dcfcdc"}}>
                    <div className={classes.container}>

                      <div className = {classes.DivCantidad}>
                          
                            <form onSubmit={handleSubmit(onSubmit)} style = {{width:"100%"}}>
                              <input
                                name = {i}
                                className = {classes.InputCantidad}
                                ref = {
                                  register({
                                    // required : {value : true, message : 'Cantidad obligatoria'}

                                  })
                                  
                                }

                                defaultValue = {value.PedidoProductosCantidad}
                                type = "number"
                              />

                              <button accessKey = {i} className = {classes.ButtonAgregarProducto}>Agregar</button>
                            </form>                    

                      </div>
                          <p>{value.ProductoCodigoBarra}</p>
                      <p className = {classes.TextNameProducto}>
                          {value.ProductoNombre}
                      </p>

                        <p><b>{value.CantidadExistente}</b> &nbsp;&nbsp; {value.ProductoUnidadMedidaNombreConsumo}</p>

                      <div className = {classes.DetalleProducto}>
                          {/* <p style = {{color:"#54a449"}}>${value.ProductoPrecioCompra}</p> */}
                      </div>
                    </div>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Grid>

      </div>
      <ButtonCarrito disabled = {false} variant="contained" color="primary" className={classes.margin} onClick = {MostrarCarrito}>
                      <ShoppingCartIcon></ShoppingCartIcon>Pedido ({TotalProductosCarrito})
      </ButtonCarrito>






                              
                              {/* seccion de alertas */}
    <div className={classes.root}>
      <div className = "AlertDiv">

        {/* Alerta de succes  */}
        <Collapse in={openSucces}>
        <Alert severity="success"
        action={
        <IconButton
          aria-label="close"
          color="inherit"
          size="small"
          onClick={() => {
          setOpenSucces(false);
          }}
          >
        <CloseIcon fontSize="inherit" />
        </IconButton>
        }
        >
           <AlertTitle><b> Aviso </b></AlertTitle>
          {ValueMensaje}
        </Alert>
          
          

        </Collapse>

        {/* Alerta de error  */}

        <Collapse in={openError}>
          <Alert severity="error"
          action={
            <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={() => {
            setOpenError(false);
              }
            }
            >
      <CloseIcon fontSize="inherit" />
      </IconButton>
      }
      >
         <AlertTitle><b> Error </b></AlertTitle>
        {ValueMensaje}
      </Alert>
      
      </Collapse>

        </div>
      </div>

</div>
  );
}

export default SeleccionarProductoPedido