import React, { Component } from 'react';
import styled from 'styled-components';
import ButtonNew from './Buttons/ButtonNewRegistro';
import { getPosts } from './../utils/RestUnidadMedida';
import MaterialTableUnidadMedida from './../components/ViewRegistros/MaterialTableUnidadMedida'



const Container = styled.div`
  margin: 1%;
  width:100%;
  overflow: auto;
`;


class CatalogoUnidadMedida extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }

  async  componentDidMount() {
        
    const UnidadMedidaFilter = {
      UnidadMedidaId : 0,
      UnidadMedidaNombre : ""
    }

    await getPosts(UnidadMedidaFilter)
    .then((res) => {
        console.log(res)
        this.setState({
            posts: res.data,
            loading : false,
        });

    });
};

  render() {
    const {posts} = this.state;
    return (
      <Container>
        <MaterialTableUnidadMedida
         posts = {posts}
        >

        </MaterialTableUnidadMedida>
     </Container>

    );
  }
}
export default CatalogoUnidadMedida;