import React, { useEffect, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import styled from 'styled-components';
import ipInfoLab from '../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import Collapse from '@material-ui/core/Collapse';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { getPosts } from '../../utils/RestGrupo';
import useToken from '../Login/useToken';



const useStyles = makeStyles((theme) => (
  {
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '100%',
      },
    },
  }));

const Container = styled.div`
margin: 1%;
width:100%;
`;


const CRUDGRUPO = (props) => {

    const {id, Mode} = props.location.state
    const GrupoId = id
    const classes = useStyles();
    const [valueGrupo, setValueGrupo] = useState(''); //UseState recupera el valor introducido
    const [ValueMensajeRest, setMensajeRest] = useState('');
    const [ValueExisteError, setExisteError] = useState(0);
    const [open, setOpen] = React.useState(false);
    const [DisabledCampos, setDisabledCampos] = React.useState(false);//si se entra en modo DSP no se podr modificar el registro
    const {TokenSdtUsuario} = useToken();
    const {UsuarioId} =  TokenSdtUsuario

    useEffect(() => {
    if(Mode == "UPD" || Mode == "DSP")
    {

      
      if (Mode === 'DSP') {
        setDisabledCampos(true)
      }

      const GrupoFilters = {
        GrupoId :GrupoId,
        GrupoNombre : "",
      }

      getPosts(GrupoFilters)
      .then((res) => {
       const data = res.data

       data.map(post => {

        const {GrupoNombre} =  post
        setValueGrupo(GrupoNombre)
       })
      
    });


    }
  },[false])

    //Por cada cambio en el textfield, se va guardando lo que el usuario va introduciendo en las funciones get
    function  getGrupoValue(e){
        const GupoValue = e.target.value;
        setValueGrupo(GupoValue)
    }


    function SaveGrupo()
    {
        
        const DataGrupo = {
                    Modo : Mode,
                    GrupoId :GrupoId,
                    GrupoNombre :  valueGrupo,
                    UsuarioId : UsuarioId
                    }

        const GuardarGrupo = new Promise((resolve, reject) => {

          const urlRest = ipInfoLab + '/rest/GuardarGrupo'
              
          axios.post(urlRest, DataGrupo).then(response => {
    
              
            const MensajeRest = response.data.Mensaje
            const ExisteError = response.data.ExisteError
            setExisteError(ExisteError)
            setMensajeRest(MensajeRest)
            
         

            if(ExisteError == 0)
            {
             
              //setTimeout(function(){window.history.back(), 3000});
              setTimeout(function(){ window.history.back(); }, 3000);
            }else
            {
              setTimeout(function(){setOpen(false);; }, 6000);
              
            }

            }).catch(reason => {
                console.log("la razon de error: " + reason);
              })
          });
          
          setOpen(true);
    }


    function ShowMessage()
    {
      if(ValueExisteError == 1)
      {

      
      return(
      <Collapse in={open}>
      <Alert severity="error"
      action={
      <IconButton
        aria-label="close"
        color="inherit"
        size="small"
        onClick={() => {
        setOpen(false);
        }}
        >
      <CloseIcon fontSize="inherit" />
      </IconButton>
      }
      >
         <AlertTitle><b> Error </b></AlertTitle>
        {ValueMensajeRest}
      </Alert>
      
  </Collapse>


      )

    }else
    {
      
      return(
        <Collapse in={open}>
        <Alert severity="success"
        action={
        <IconButton
          aria-label="close"
          color="inherit"
          size="small"
          onClick={() => {
          setOpen(false);
          }}
          >
        <CloseIcon fontSize="inherit" />
        </IconButton>
        }
        >
           <AlertTitle><b> Aviso </b></AlertTitle>
          {ValueMensajeRest}
        </Alert>
        
    </Collapse>
      )
    }
    }

    return (
  
     <Container>

            {Mode == "INS" &&<h3>Nuevo grupo</h3>}
            {Mode == "UPD" &&<h3>Modificar grupo</h3>}
            {Mode == "DSP" &&<h3>Visualizar gupo</h3>}

            <div className = "cardNewRegistro">
                <div className = "jss1 jss5 jss2">

                <form className={classes.root} noValidate autoComplete="off">
                  <TextField disabled  = {DisabledCampos} id="Grupo" label="Grupo" variant="outlined" onChange={getGrupoValue} value = {valueGrupo} fullWidth = {true}/> 
                </form>

                <button disabled  = {DisabledCampos} type="button" className = {!DisabledCampos ? "btn btn-green-light btn-pill"  : "btn btn-gray-light btn-pill "}  onClick = {SaveGrupo} >Guardar Grupo</button>
                
                               
              <div className = "AlertDiv">
                  
                  <ShowMessage/>
              </div>

              </div>
            </div>
        
    </Container>

    );
}


export default CRUDGRUPO;
