import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTablePedido from './../components/ViewRegistros/MaterialTablePedidos'
import { getPostsPedido } from './../utils/RestPedido';
const Container = styled.div`
margin: 1%;
width:100%;
`;


class CatalogoPedidos extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
      PedidoId : 0,
           };
  }

  async  componentDidMount() {
        
           const PedidoFilters = {
               PedidoId : 0,
               ProveedorId : 0
           }
  
           await getPostsPedido(PedidoFilters)
           .then((res) => {
               //console.log(res)
              this.setState({
                   posts: res.data,
                   loading : false,
                   PedidoId : 0,
               });
  
           });
       };

render()
{
  const {posts} = this.state;
  
  return(
    <Container>
      <div style = {{width:"100%",   height: "100%", 
           overflowX:"auto"}}
            >

      <MaterialTablePedido
      posts = {posts}/>

      </div>
     
    </Container>
    
  )
 
}

}
export default CatalogoPedidos;