import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTableAlmacenes from './../components/ViewRegistros/MaterialTableAlmacenes'
import { getPostsAlmacen } from './../utils/RestAlmacen';


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoAlmacenes extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }


  async  componentDidMount() {
        
    const AlmacenFilters = {
      AlmacenId : 0,
      AlmacenNombre : "",
      MostrarDefault : false,
    }

    await getPostsAlmacen(AlmacenFilters)
    .then((res) => {
        this.setState({
            posts: res.data,
            loading : false,
        });

    });
};

 render() {

  const {posts} = this.state;

    return (
       <Container>
        
        <MaterialTableAlmacenes 
         posts = {posts}
         />
      </Container>
 
    );
  }
}
export default CatalogoAlmacenes;