import React from 'react'
import TextField from '@material-ui/core/TextField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import Switch from '@material-ui/core/Switch';
import FormGroup from '@material-ui/core/FormGroup';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import { getPostsProveedor } from './../../utils/RestProveedor';
import { withStyles } from '@material-ui/styles';
import { getPostsTipoProducto } from './../../utils/RestTipoProducto';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const styles = theme => ({
    TextCenter: {
        "& $div": {
            justifyContent: "center",
            "& $input": {
              width: "100%",
              paddingLeft:"40%"
            }
          },
          "& $p": {
            alignSelf: "center"
          }
    },
  });



  

class ModalFiltrosKardex extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            valueFiltroExistencias: this.props.valueFiltroExistencias,//"Todos",
            valueFiltroCaducidad: this.props.valueFiltroCaducidad,
            valueDiasCaduca : this.props.valueDiasCaduca,
            valueFiltroReactivo : this.props.valueFiltroReactivo,
            valueFiltroConsumible : this.props.valueFiltroConsumible,
            valueFiltroProveedor : this.props.valueFiltroProveedor,
            posts: [],
            postsTipoProducto: this.props.valueTipoProductoFiltro,
            loading: true,
        }
    }

    
async componentDidMount(previousProps, previousState)
{
    const ProveedorFilters = {
         ProveedorId : 0,
         ProveedorNombre: "",
         ProductoCodigoBarra: ""
       }
    
         await getPostsProveedor(ProveedorFilters)
    
           .then((res) => {
              this.setState({
                posts: res.data,
              });
           })
           .catch((err) => console.log(err));

          const { posts } = this.state;
}


renderPostsProductosTipo = () =>
{
    // const useStyles = makeStyles((theme) => ({
    //     root: {
    //       flexGrow: 1,
    //     },
    //     paper: {
    //       padding: theme.spacing(2),
    //       textAlign: 'center',
    //       color: theme.palette.text.secondary,
    //     },
    //   }));

    // const classes = useStyles();
    
    const getTipoProductoSeleccionado = (TipoProductoId) =>
    {
   
        const {postsTipoProducto} = this.state;

        const newTipoProductochecked = postsTipoProducto.map(Response => {
        const  TipoProductoSeleccionado = Response.TipoProductoSeleccionado

            if(TipoProductoId == Response.TipoProductoId)
            {
                return{...Response, TipoProductoSeleccionado :  !TipoProductoSeleccionado};
            }
            else
            {
                return{...Response, TipoProductoSeleccionado :  TipoProductoSeleccionado};
            }

        })


        this.setState({
            postsTipoProducto : newTipoProductochecked,
        })

        this.props.parentCallbackFiltroTiposProducto(newTipoProductochecked)
    }

    const {postsTipoProducto} = this.state;


    return(
        <Grid container spacing={3}>
        {postsTipoProducto.map(row => (
          <Grid item xs={6}>
           
             
                <Grid container spacing={3}>
                  <Grid item xs={4} sm={4}>
                    <Grid container>
                      <Grid container justify="space-evenly">
                        <FormControl component="fieldset">
                        <FormGroup aria-label="position" row>
                            <FormControlLabel
                            control={<Switch
                            color="primary"
                            checked={row.TipoProductoSeleccionado}
                           onClick={() => getTipoProductoSeleccionado(row.TipoProductoId)}
                            value="valueFiltroReactivo"
                            inputProps={{ 'aria-label': 'secondary checkbox' }}
                            />}
                            label= {row.TipoProductoNombre}
                        />

                        </FormGroup>
                        </FormControl>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              
        
          </Grid>
        ))}
      </Grid>
    )
}




renderPosts = (previousProps) => 
{
    
        const { posts, valueFiltroProveedor } = this.state;

        return posts.map(post => {
          
          const { ProveedorNombre, ProveedorDireccion, ProveedorId } = post;
          if (valueFiltroProveedor == ProveedorId)
          {
            return (
                <option selected value={ProveedorId}>{ProveedorNombre}</option>
              ); 
          }
          else
          {
            return (
                <option value={ProveedorId}>{ProveedorNombre}</option>
              ); 
          }

        });
     
}



    render(){



        const getFiltroExistencias = event =>{
            const FiltroExistenciasValue = event.target.value;
            this.setState({valueFiltroExistencias : FiltroExistenciasValue})
            this.props.parentCallbackFiltroExistencias(FiltroExistenciasValue)
        }

        const getFiltroCaducidad = event =>{
            const FiltroCaducidadValue = event.target.value;
            this.setState({valueFiltroCaducidad : FiltroCaducidadValue})
            this.props.parentCallbackFiltroCaducidad(FiltroCaducidadValue)
        }

        const getDiasCaduca = event => {
            const DiasCaducaValue = event.target.value;
            this.setState({valueDiasCaduca : DiasCaducaValue})
            this.props.parentCallbackDiasCaduca(DiasCaducaValue)
        }

        const getfiltroReactivo = event => {
            const FiltroReactivoValue = !this.state.valueFiltroReactivo
            this.setState({ valueFiltroReactivo:  FiltroReactivoValue});
            this.props.parentCallbackFiltroReactivo(FiltroReactivoValue)
        }

        const getfiltroConsumible = event => {

            const FiltroConsumibleValue = !this.state.valueFiltroConsumible
            this.setState({ valueFiltroConsumible:  FiltroConsumibleValue});
            this.props.parentCallbackFiltroConsumible(FiltroConsumibleValue)
            
        }

        const getfiltroProveedor = event => {
            const ProveedorValue = event.target.value
            this.setState({valueFiltroProveedor : ProveedorValue})
            this.props.parentCallbackFiltroProveedor(ProveedorValue)
        }
 
       
      

        const valueFiltroExistencias = this.state.valueFiltroExistencias;
        const valueFiltroCaducidad = this.state.valueFiltroCaducidad;
        const valueDiasCaduca = this.state.valueDiasCaduca;
        const valueFiltroReactivo = this.state.valueFiltroReactivo;

        const { classes } = this.props;

       function FormRow() {
            return (
              <React.Fragment>
                <Grid item xs={4}>
                  <Paper>item</Paper>
                </Grid>
                <Grid item xs={4}>
                  <Paper>item</Paper>
                </Grid>
                <Grid item xs={4}>
                  <Paper>item</Paper>
                </Grid>
              </React.Fragment>
            );
          }

        return(
            <div>
            <h4 style = {{height:"10px"}}>Existencias</h4>
              <div style = {{paddingLeft:"20%"}}>
                <FormControl component="fieldset" style = {{height:"100px"}}>
                 <RadioGroup aria-label="Exitencias" name="Exitencias" value={valueFiltroExistencias} onChange= {getFiltroExistencias}>
                     <FormControlLabel value="Todos" control={<Radio color="primary"/>} label="Todos" />
                     <FormControlLabel value="PMinimo" control={<Radio color="primary"/>} label="Por debajo del punto mnimo" />
                     <FormControlLabel value="PPedido" control={<Radio color="primary"/>} label="Por debajo del punto de pedido" />
                 </RadioGroup>
                </FormControl>
                </div>
             
            <h4>Caducidad</h4>
             <div style = {{paddingLeft:"20%"}}>
             <FormControl component="fieldset" style = {{height:"100px"}}>
                 <RadioGroup aria-label="Caducidad" name="Caducidad" value={valueFiltroCaducidad} onChange={getFiltroCaducidad}>
                     <FormControlLabel value="Todos" control={<Radio color="primary"/>} label="Todos" />
                     <FormControlLabel value="Caducados" control={<Radio color="primary"/>} label="Caducados" />
                     <div style = {{display:"flex"}}>
                     <FormControlLabel value="DiasCaduca" control={<Radio color="primary"/>} label="Caducan en:" />
                         <TextField
                         id="CaducidadDias"
                         type="number"
                         onChange = {getDiasCaduca}
                         className={classes.TextCenter}
                         value = {valueDiasCaduca}
                         />

                         <p>das</p>
                     </div> 

                  </RadioGroup> 
              </FormControl> 
             </div>

            <div id="DivProducto">

                <h4>Producto</h4>
                <div style = {{paddingLeft:"20%"}}>

                    
                {this.renderPostsProductosTipo()}


                    
                    
                    <div style = {{width:"100%"}}>
                        <FormControl variant="outlined" className="" fullWidth = {true} >
                        <InputLabel htmlFor="outlined-age-native-simple">Proveedor</InputLabel>
                        <Select
                        native
                        fullWidth={true}
                        defaultValue = {0}
                        onChange={getfiltroProveedor}
                        label="Proveedor"
                        inputProps={{
                        name: 'Proveedor',
                        id: 'outlined-age-native-simple',
                        }}
                        >
                     
                        {/* <option aria-label="None" value={0} /> */}
                        <option value={0}>Todos</option>
                        {this.renderPosts()}
                        </Select>
                      
                        </FormControl>
                    </div>
                </div>
             </div>
        </div>
        )
    }
}

export default  withStyles(styles)(ModalFiltrosKardex);