import React, { useState } from 'react';
import Aside from './Aside';
import Main from './Main';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import Media from 'react-media';

import Home from './../../Home'
//Catalogos
import CatalogoAlmacenes from './../../CatalogoAlmacenes'
import CatalogoGrupos from './../../CatalogoGrupos'
import CatalogoSecciones from './../../CatalogoSecciones'
import CatalogoPedidos from './../../CatalogoPedidos'
import CatalogoProveedores from './../../CatalogoProveedores'
import CatalogoUnidadMedida from './../../CatalogoUnidadMedida'
import CatalogoMotivoMovimiento from './../../CatalogoMotivoMovimiento'
import CatalogoClasificacion from './../../CatalogoClasificacion'
import CatalogoProducto from './../../CatalogoProducto'
import Kardex from './../../Kardex'
import CatalogoLog from './../../CatalogoLog'
import CatalogoPerfil from './../../CatalogoPerfil'
import CatalogoUsuario from './../../CatalogoUsuarios'


//Inserts
import NuevoProveedor from './../../NewRegistros/CRUDPROVEEDOR'
import NuevoAlmacen from '../../NewRegistros/CRUDALMACEN'
import CRUDSECCION from '../../NewRegistros/CRUDSECCION'
import CRUDGRUPO from '../../NewRegistros/CRUDGRUPO'
import NuevoUnidadMedida from '../../NewRegistros/CRUDUNIDADMEDIDA'
import NuevoMotivoMovimiento from '../../NewRegistros/CRUDMOTIVOMOVIMIENTO'
import NuevaClasificacion from '../../NewRegistros/CRUDCLASIFICACION'
import CRUDPRODUCTO from './../../NewRegistros/CRUDPRODUCTO'
import NuevoPedido from '../../NewRegistros/CRUDPEDIDO'
import CRUDPERFIL from '../../NewRegistros/CRUDPERFIL'

//Views
import ViewAlmacen from './../../ViewRegistros/ViewAlmacen';
import ViewProveedor from './../../ViewRegistros/ViewProveedor';
import ViewUnidadMedida from '../../NewRegistros/CRUDUNIDADMEDIDA';
import ViewMotivoMovimiento from './../../NewRegistros/CRUDMOTIVOMOVIMIENTO';
import ViewClasificacion from './../../NewRegistros/CRUDCLASIFICACION';
import ProductoHistorico from './../../ViewRegistros/ProductoHistorico'

//Updates
import UpdateProveedor from './../../NewRegistros/CRUDPROVEEDOR';
import UpdateUnidadMedida from '../../NewRegistros/CRUDUNIDADMEDIDA';
import UpdateMotivoMovimiento from '../../NewRegistros/CRUDMOTIVOMOVIMIENTO';
import UpdateClasificacion from '../../NewRegistros/CRUDCLASIFICACION';
import CRUDALMACEN from '../../NewRegistros/CRUDALMACEN';
import UpdatePedido from '../../NewRegistros/CRUDPEDIDO'

//Procesos
import RecibirProductosPedido from '../../Acciones/RecibirProductosPedido'
import AsignarPermisosMenu from '../../Acciones/AsignarPermisosMenu'






function Layout({ setLocale, TokenSdtUsuario,  ...rest }) {
 

  const [rtl, setRtl] = useState(false);
  const [collapsed, setCollapsed] = useState(false);
  const [image, setImage] = useState(true);
  const [toggled, setToggled] = useState(false);
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleCollapsedChange = (checked) => {
    setCollapsed(checked);
  };

  const handleRtlChange = (checked) => {
    setRtl(checked);
    setLocale(checked ? 'ar' : 'en');
  };
  const handleImageChange = (checked) => {
    setImage(checked);
  };

  const handleToggleSidebar = (value) => {
    setToggled(value);
  };

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  return (
    <div className={`app ${rtl ? 'rtl' : ''} ${toggled ? 'toggled' : ''}`}>
      {/* <Media query="(max-width: 599px)" render={() =>
          (
            <p>I am small!</p>
          )}
        /> */}

    
      <BrowserRouter>
      <diV>
      <Aside
        image={image}
        collapsed={collapsed}
        rtl={rtl}
        toggled={toggled}
        handleToggleSidebar={handleToggleSidebar}
        
      />
      </diV>
      


<Main
        image={image}
        toggled={toggled}
        collapsed={collapsed}
        rtl={rtl}
        handleToggleSidebar={handleToggleSidebar}
        handleCollapsedChange={handleCollapsedChange}
        handleRtlChange={handleRtlChange}
        handleImageChange={handleImageChange}
      />
      
      
      <Route exact path = "/" component = {Home}/>

      <Route exact path = "/CatalogoAlmacenes" component = {CatalogoAlmacenes}/>
      <Route exact path = "/CatalogoGrupos" component = {CatalogoGrupos}/>
      <Route exact path = "/CatalogoSecciones" component = {CatalogoSecciones}/>
      <Route exact path = "/CatalogoProveedores" component = {CatalogoProveedores}/>
      <Route exact path = "/CatalogoUnidadMedida" component = {CatalogoUnidadMedida}/>
      <Route exact path = "/CatalogoMotivoMovimiento" component = {CatalogoMotivoMovimiento}/>
      <Route exact path = "/CatalogoClasificacion" component = {CatalogoClasificacion}/>
      <Route exact path = "/CatalogoProducto" component = {CatalogoProducto}/>
      <Route exact path = "/Kardex" component = {Kardex}/>
      <Route exact path = "/CatalogoPedidos" component = {CatalogoPedidos}/>

      {/* Seguridad */}
      <Route exact path = "/CatalogoLog" component = {CatalogoLog}/>
      <Route exact path = "/CatalogoPerfiles" component = {CatalogoPerfil}/>
      <Route exact path = "/CatalogoUsuarios" component = {CatalogoUsuario}/>
      

      <Route exact path = "/NuevoProveedor" component = {NuevoProveedor}/>
      <Route exact path = "/NuevoAlmacen" component = {NuevoAlmacen}/>
      <Route exact path = "/NuevaSeccion" component = {CRUDSECCION}/>
      <Route exact path = "/NuevoGrupo" component = {CRUDGRUPO}/>
      <Route exact path = "/NuevoUnidadMedida" component = {NuevoUnidadMedida}/>
      <Route exact path = "/NuevoMotivoMovimiento" component = {NuevoMotivoMovimiento}/>
      <Route exact path = "/NuevoClasificacion" component = {NuevaClasificacion}/>
      <Route exact path = "/NuevoProducto" component = {CRUDPRODUCTO}/>
      <Route exact path = "/NuevoPedido" component = {NuevoPedido}/>
      <Route exact path = "/NuevoPerfil" component = {CRUDPERFIL}/>
      
      
      <Route exact path = "/ViewAlmacen" component = {CRUDALMACEN}/>
      <Route exact path = "/ViewSeccion" component = {CRUDSECCION}/>
      <Route exact path = "/ViewGrupo" component = {CRUDGRUPO}/>
      <Route exact path = "/ViewProveedor" component = {ViewProveedor}/>
      <Route exact path = "/ViewUnidadMedida" component = {ViewUnidadMedida}/>
      <Route exact path = "/ViewMotivoMovimiento" component = {ViewMotivoMovimiento}/>
      <Route exact path = "/ViewClasificacion" component = {ViewClasificacion}/>
      <Route exact path = "/ProductoHistorico" component = {ProductoHistorico}/>
      <Route exact path = "/ViewPerfil" component = {CRUDPERFIL}/>
            
      <Route exact path = "/UpdateProveedor" component = {UpdateProveedor}/>
      <Route exact path = "/UpdateAlmacen" component = {CRUDALMACEN}/>
      <Route exact path = "/UpdateUnidadMedida" component = {UpdateUnidadMedida}/>
      <Route exact path = "/UpdateMotivoMovimiento" component = {UpdateMotivoMovimiento}/>
      <Route exact path = "/UpdateClasificacion" component = {UpdateClasificacion}/>
      <Route exact path = "/UpdateProducto" component = {CRUDPRODUCTO}/>
      <Route exact path = "/UpdatePedido" component = {UpdatePedido}/>
      <Route exact path = "/UpdatePerfil" component = {CRUDPERFIL}/>
      <Route exact path = "/UpdateSeccion" component = {CRUDSECCION}/>
      <Route exact path = "/UpdateGrupo" component = {CRUDGRUPO}/>

      <Route exact path = "/ViewPedido" component = {UpdatePedido}/>
      <Route exact path = "/RecibirProductosPedido" component = {RecibirProductosPedido}/>
      <Route exact path = "/AsignarPermisosMenu" component = {AsignarPermisosMenu}/>
      
      {/* <Route exact path = "/IniciarSesion" component = {Login}/> */}
      
            
<Switch>
</Switch>
    </BrowserRouter>
     

   
    </div>
  );
}

export default Layout;
