import React, { Component } from 'react';
import styled from 'styled-components';
import ProveedorRegistros from '../containers/ProveedorRegistros';
import ButtonNew from './Buttons/ButtonNewRegistro';
import { getPostsProveedor } from '../utils/RestProveedor';
import MaterialTableProveedores from './ViewRegistros/MaterialTableProveedores'


 const Container = styled.div`
 margin: 1%;
 width:100%;
 overflow: auto;
  `;

 class CatalogoProveedores extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }

  async  componentDidMount() {
    const ProveedorFilters = {
      ProveedorId : 0,
      ProveedorNombre : "",
      ProductoCodigoBarra:"",
    }

    await getPostsProveedor(ProveedorFilters)
    .then((res) => {
      console.log('res en catalogoproveedor: ')
      console.log(res)
      this.setState({
        posts :  res.data,
        loading : false,
      });
    });
  };

  render(){
    const {posts} = this.state;
    return(
        <Container>
          <MaterialTableProveedores
          posts = {posts}
          >

          </MaterialTableProveedores>
        </Container>
    )
  }
    
 }

 export default CatalogoProveedores;