/*eslint-disable*/
import React, { Component } from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// nodejs library that concatenates classes
import classnames from "classnames";
import Button from "./../CustomButtons/Button.js";

export default function FixedPluginPedido(props) {
    const [classes, setClasses] = React.useState("dropdown show");
    const [bg_checked, setBg_checked] = React.useState(true);
    const [bgImage, setBgImage] = React.useState(props.bgImage);
    const [CantidadACopiar, setCantidadACopiar] = React.useState(0);
    const [CaducidadACopiar, setCaducidadACopiar] =  React.useState("");
    const [LoteACopiar, setLoteACopiar] = React.useState("")

  const handleClick = () => {
    props.handleFixedClick();
  };

  function getCantidadCopiar(e){
    const cantidad = e.target.value
    setCantidadACopiar(cantidad)
    }

    function getFechaCaducidadCopiar(e){
    const caducidad = e.target.value
    setCaducidadACopiar(caducidad)
    
    }

    function getLoteCopiar(e){
        const Lote = e.target.value
        setLoteACopiar(Lote)
        
    }

  
  const copiarCantidad = () => {
    props.handleCantidadCopiar(CantidadACopiar)
  };

  const copiarFechaCaducidad = () => {
      props.handleCaducidadCopiar(CaducidadACopiar)
  }

  const copiarLote = () => {
      props.handleLoteCopiar(LoteACopiar)
  }

  return (
    <div
      className={classnames("fixed-plugin", {
        "rtl-fixed-plugin": props.rtlActive
      })}
    >
      <div id="fixedPluginClasses" className={props.fixedClasses}>
        <div onClick={handleClick}>
          <i className="fa fa-cog fa-2x" />
        </div>
        <ul className="dropdown-menu">
          <li className="header-title">Datos</li>

         

            <li className="button-container">
              <div style = {{display : "flex"}}>
                <input id="InputCantidad" type="text" placeholder="Cantidad" style = {{height : "25px"}} defaultValue = {0} onChange = {getCantidadCopiar}></input>
                    <div className="button-container">
                        <Button
                        color="success"
                        fullWidth
                        style = {{margin :0, height : 33}}
                        onClick = {copiarCantidad}
                        >
                        <i className="fa fa-clone fa-1x" />
                        </Button>
                    </div>
                </div>
            </li>
            <li className="button-container">
                <div style = {{display : "flex"}}>
                    <input id="InputFechaCaduidad" type="date" placeholder="Caducidad" style = {{height : "25px", width : "60%"}} defaultValue = "" onChange = {getFechaCaducidadCopiar}></input>
                    <div className="button-container">
                        <Button
                        color="warning"
                        fullWidth
                        style = {{margin :0, height : 33}}
                        onClick = {copiarFechaCaducidad}
                        >
                        <i className="fa fa-clone fa-1x" />
                        </Button>
                    </div>
                </div>
            </li>
            <li className="button-container">
                <div style = {{display : "flex"}}>
                <input id="InputLote" type="text" placeholder="No. Lote" style = {{height : "25px", width : "60%"}} defaultValue = "" onChange = {getLoteCopiar}></input>
                    <div className="button-container">
                        <Button
                        color="info"
                        fullWidth
                        style = {{margin :0, height : 33}}
                        onClick ={copiarLote}
                        >
                        <i className="fa fa-clone fa-1x" />
                        </Button>
                    </div>
                </div>
          </li>
        
        </ul>
      </div>
    </div>
  );
}

FixedPluginPedido.propTypes = {
  bgImage: PropTypes.string,
  handleFixedClick: PropTypes.func,
  rtlActive: PropTypes.bool,
  fixedClasses: PropTypes.string,
  bgColor: PropTypes.oneOf(["purple", "blue", "green", "orange", "red"]),
  handleColorClick: PropTypes.func,
  handleImageClick: PropTypes.func
};
