import React, {useState, useEffect} from 'react';
import MaterialTable from 'material-table';
import { makeStyles } from '@material-ui/core/styles';
import ButtonNew from '../Buttons/ButtonNewRegistro'//'././Buttons/ButtonNewRegistro';
import MenuPopupState from './../../MenuGrids/MenuMaterialTableCatalogos'
import useToken from './../Login/useToken';
import {GetPermisosAccionPantallaPerfil} from './../../containers/Functions/GetPermisosAccionPantallaPerfil';

const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  
    table: {
      minWidth: 700,
    },
  
    margin: {
      margin: theme.spacing(1),
      backgroundColor: '#ecf6fd',
      border: 0,
      fontWeight: 'bold',
      color: '#2797ef',
    },
  
  }));


const MaterialTableMotivoMovimiento = React.memo((props) =>
{

    const {posts} =  props;
    

    const classes = useStyles();

    const [dataMaterialTable, setdataMaterialTable] = useState([])

    //Se obtienen los datos del usuario logueado
    const {TokenSdtUsuario} = useToken();
    const {UsuarioId, PerfilId} =  TokenSdtUsuario

    //Variables donde se guardan las acciones permitidas dentro de la pantalla
    const [PermisoAGREGARMOTIVOMOVIMIENTO, setPermisoAGREGARMOTIVOMOVIMIENTO] = useState(false)
    const [PermisoMODIFICARMOTIVOMOVIMIENTO, setPermisoMODIFICARMOTIVOMOVIMIENTO] = useState(false)

    useEffect (() => {
      //Servicio para recuperar las acciones permitidas del usuario [INICIO]
      const FiltersPermisos = {
        PerfilId : PerfilId,
        OpcionId : 0,
        OpcionUrl : '/CatalogoMotivoMovimiento'
      }

      const PermisosMenu =  GetPermisosAccionPantallaPerfil(FiltersPermisos)
      .then((response) => {

        return {response}

      })

      var getPermisos = Promise.resolve(PermisosMenu);
      getPermisos.then(function(value) {

      const {SDTPerfilUsuarioOpcion} =  value.response.data
      const {Accion} = SDTPerfilUsuarioOpcion


      
      const DataWithMenu = posts.map(Response => {
      const {MotivoMovimientoId} = Response;
    
      


      //Se arman las opciones que tendr el men delmaterial table
      let OpcionesMenuMaterialTable = []
      var {permisoModificar} = false
      
      Accion.map((accion) => {
        const {AccionId, AccionAcceso} = accion

        switch (AccionId) {
          case "AGREGARMOTIVOMOVIMIENTO":
           setPermisoAGREGARMOTIVOMOVIMIENTO(AccionAcceso)
           break;

         case "MODIFICARMOTIVOMOVIMIENTO":
          permisoModificar = AccionAcceso
          setPermisoMODIFICARMOTIVOMOVIMIENTO(AccionAcceso)
          break;
        
         default:
           break;
        }

      })

      OpcionesMenuMaterialTable = [
        {
          pathname : "/ViewMotivoMovimiento",
          state : [{
            Mode : "DSP",
            id : MotivoMovimientoId,
          }],
      
          TituloOpcion : "Visualizar"
        }
      ]

      if(permisoModificar)
      {
        let datosmenu = [...OpcionesMenuMaterialTable,
          {
            pathname : "/UpdateMotivoMovimiento",
             state : [{
               Mode : "UPD",
               id : MotivoMovimientoId,
             }],
  
             TituloOpcion : "Modificar"
          }
         ]

         OpcionesMenuMaterialTable = datosmenu
      }

      return{...Response, DataWithMenu: <MenuPopupState OpcionesMenuMaterialTable= {OpcionesMenuMaterialTable}> </MenuPopupState>}
    
    })
      //Servicio para recuperar las acciones permitidas del usuario [FIN]
    
      setdataMaterialTable(DataWithMenu)
    })

    }, [props])
    // const DataWithMenu = posts.map(Response => {
    //   const {MotivoMovimientoId} = Response;
    
    //   return{...Response, DataWithMenu: <MenuPopupState FMotivoMovimientoId = {MotivoMovimientoId}> </MenuPopupState>}
    // })
    
    
    // const dataMaterialTable =  DataWithMenu;
    

    return(
        <div>

            <MaterialTable
                title={
                    <>Motivos Movimientos
                    {
                      PermisoAGREGARMOTIVOMOVIMIENTO &&
                      <ButtonNew
                      UrlButton = {"/NuevoMotivoMovimiento"}
                      TittleButton = {"Agregar Motivo Movimiento"}
                      Mode = "INS"
                      />
                    }
                   
                   </>
                }

                columns = {[
                    { title: 'Movimiento', field: 'MotivoMovimientoNombre',
                      cellStyle: {
                        width: '20%',
                     
                      },
                    },
                    { title: 'Tipo', field: 'MotivoMovimientoTipo',
                    cellStyle: {
                      width: '20%',
                   
                    },
                   },
                   
                    { title: '', field: 'DataWithMenu',
                    cellStyle: {
                      width: '1%',
                   
                    },
                   },
                    
                   
                    
                    
                    
                    
                ]}

                data= {dataMaterialTable}//{posts}

                options={{
                      
                     headerStyle: {
                     backgroundColor: '#4FC3A1',
                     color: '#ffffff',
                     padding: 8,
                     position: 'sticky', 
                     top: 0
                     },
                     rowStyle: {
                     fontSize:16,
                     height:10,
                     width:20,
                    },
                    cellStyle: {
                     width: 200,
                     
                   },
                   
                }}

                localization={{
                    toolbar: {
                      searchPlaceholder: 'Buscar Motivo Movimiento',
                      
                    },
            
                    pagination: {
                      labelDisplayedRows: '{from}-{to} de {count}',
                      labelRowsSelect: 'Motivos Movimientos por pgina',
                  },
                }}
            />
        </div>
    )

})

export default MaterialTableMotivoMovimiento