import axios from 'axios';
import ipInfoLab from './../containers/Functions/GetIpInfolab';

export function getPostsProductoHistorico(ProductoHistoricoFiltros){
    const {ProductoId} = ProductoHistoricoFiltros
    const urlRest = ipInfoLab + '/rest/ConsultaProductoHistorico?&ProductoId=' + ProductoId

    axios.delete(urlRest)
    return axios.get(urlRest)
}