import { BorderBottom, BorderColor, Class } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components';
import { getPostsPedido } from '../../utils/RestPedido';
import $ from 'jquery';
import IconSearch from './../../Image/searchicon.png';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import ipInfoLab from '../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import Collapse from '@material-ui/core/Collapse';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import FixedPluginPedido from '../FixedPlugin/FixedPluginPedido'



const Container = styled.div`
margin: 1%;
width:98%;

`;


const ButtonGuardar = withStyles((theme) => ({
  root: {
   
    color: "white",
    backgroundColor: "#15b765",
    '&:hover': {
      backgroundColor: "#0a86d4",
    },
    borderRadius:100,
    // padding:"10px",
    height:60,
    boxShadow: "0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19)",
    bottom:10,
    right:10,
    position:"fixed",
    zIndex:100
  },
}))(Button);

const RecibirProductosPedido = React.memo(class RecibirProductosPedido extends React.Component{

    constructor(props) {
        super(props);
        
        this.state = {
          posts: [],
          loading: true,
          PedidoId : props.location.state.id,
          ProductosSeleccionados : [],
          AlmacenId : 0,
          OpenSucces : false,
          OpenError : false,
          MensajeRest : '',
          ExisteError : 0,
          fixedClasses : "dropdown", 
   
        };
    }


   

    async componentDidMount()
    {

      const {PedidoId} = this.state



      const PedidoFilters = {
        PedidoId : PedidoId,
        ProveedorId : 0
      }

      await getPostsPedido(PedidoFilters)
      .then((res) => {

        const {data} = res
        const {Productos, AlmacenPedidoId} = data[0]


        const ProdusctosConCantidadRecibida = Productos.map(response => {
          return{...response, ProductoCantidadRecibida : response.PedidoProductosCantidadPendiente}
        })

        const ProductosConFechaCaducidad = ProdusctosConCantidadRecibida.map(response => {
          return{...response, PedidoProductosFechaCaducidad : ""}
        })

       
        this.setState({
          ProductosSeleccionados: ProductosConFechaCaducidad,
          AlmacenId : AlmacenPedidoId
      });

      })


      $(document).ready(function(){
        $("#InputSearchProductoPedido").on("keyup", function() {
          var value = $(this).val().toLowerCase();
          $("#myTable tr").filter(function() {
            $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1)
          });
        });
      });

    }


    
    GetCantidad = (e) =>
    {
      const {ProductosSeleccionados, PedidoId, AlmacenId} = this.state
     
      const CantidadRecibida = e.target.value
      const ItemModificado = e.target.name
     
      ProductosSeleccionados[ItemModificado].ProductoCantidadRecibida = CantidadRecibida
      
      this.setState({
        ProductosSeleccionados : ProductosSeleccionados,
      })
     
    }

    GetFechaCaducidad = (e) => {
      const {ProductosSeleccionados, PedidoId, AlmacenId} = this.state

      const FechaCaducidad = e.target.value
      const ItemModificado = e.target.name

      ProductosSeleccionados[ItemModificado].PedidoProductosFechaCaducidad = FechaCaducidad

      this.setState({
        ProductosSeleccionados : ProductosSeleccionados,
      })

    }

    GetLote = (e) => {
      const {ProductosSeleccionados, PedidoId, AlmacenId} = this.state

      const Lote = e.target.value
      const ItemModificado = e.target.name

      ProductosSeleccionados[ItemModificado].ProductoLote = Lote

      this.setState({
        ProductosSeleccionados : ProductosSeleccionados,
      })
   

    }

    RecibirProductos = () => {
      const {PedidoId, AlmacenId, ProductosSeleccionados} = this.state
     
      const DatosUsuario = () => {
        const tokenString = localStorage.getItem('SdtUsuario');
        const userToken = JSON.parse(tokenString);
        return userToken
      }

      const {UsuarioId} = DatosUsuario()
     

     const ProductosPedido = ({
      SdtPedido : //Este es el nombre del parmetro en GX.
      {
        PedidoId : PedidoId,
        PedidoFechaHora : "",
        PedidoEstatus : 0,
        PedidoObservaciones : "",
        AlmacenPedidoId : AlmacenId,
        Productos : ProductosSeleccionados,
        
       
      }, 

      UsuarioId : UsuarioId
     })

     const GuardarRecepcionPedido = new Promise((resolve, reject) => {
      const urlRest = ipInfoLab + '/rest/RecibirProductosPedido'

      axios.post(urlRest, ProductosPedido)
      .then(response => {
        var MensajeRest = response.data.MensajeRest
        var ExisteError = response.data.ExisteError


        if(ExisteError)
        {
          this.setState({
            OpenSucces : false,
            OpenError : true
          })
        }else
        {
          this.setState({
            OpenSucces : true,
            OpenError : false
          })

          setTimeout(
            function()
            {


            window.history.back();
            }, 3000
          );
        }

        this.setState({
          // OpenSucces : ((ExisteError = 0) ? false : true),
          // OpenError : ((ExisteError > 0) ? true : false),
          MensajeRest : MensajeRest,
          ExisteError : ExisteError,
        })

 


      })
      .catch((err) => console.log("Error al generar movimiento: " + err));
     })

     
    }


  handleFixedClick = () => {
    const {fixedClasses} = this.state

    if (fixedClasses === "dropdown") {
      this.setState({fixedClasses : "dropdown show"})
    } else {
      this.setState({fixedClasses : "dropdown"})
    }
  }

  getCantidadCopiar = (cantidadacopiar) => {
    const {ProductosSeleccionados} = this.state
    const Datos = ProductosSeleccionados.map(response => {
      response.ProductoCantidadRecibida = cantidadacopiar
 
      return{...response}
    })
   
    this.setState({ProductosSeleccionados : Datos})
  }

  getFechaCaducidadCopiar = (CaducidadCopiar) => {
    const {ProductosSeleccionados} = this.state
    const Datos = ProductosSeleccionados.map(response => {
      response.PedidoProductosFechaCaducidad = CaducidadCopiar
      return{...response}
    })

    this.setState({ProductosSeleccionados : Datos})
  }

  getLoteCopiar = (LoteCopiar) => {
    const {ProductosSeleccionados} = this.state
    const Datos = ProductosSeleccionados.map(response => {
      response.ProductoLote = LoteCopiar
      return{...response}
    })

    this.setState({ProductosSeleccionados : Datos})
  }

    render()
    {

     
      const {ProductosSeleccionados, PedidoId, OpenSucces, OpenError, MensajeRest, fixedClasses} = this.state;
     
          const table = {
            textAlign : "left",
            position : "relative",
            bordercollapse : "collapse",
            width: '100%'
          }

          const th = {
            backgroundColor : "rgb(1, 87, 155)",
            color: "white",
            position : "sticky",
            top : 0,
            boxShadow : "0 2px 2px -1px rgba(0, 0, 0, 0.4)",
            padding: "20px",
            textAlign : "center",
          }

          const td = {
            backgroundColor : "transparent",
            padding :"12px 8px",
            margin : 0,
            fontSize: "0.8125rem",
            fontfamily : 'Roboto", "Helvetica", "Arial", sans-serif',
            fontWeight : 300,
            lineheight : 1.42857143,
            textAlign : "center",
            color: "rgba(0, 0, 0, 0.87)",
            
          }

          const tr = {
            backgroundColor : "white",
            borderBottom : "1pt solid",
            borderColor : "#E7E2E1",
            '&:hover': {
                backgroundColor: "#red",
            },
          }

          const InputSearch = {
            width: "330px",
            boxSizing: "border-box",
            border : "2px solid #ccc",
            borderRadius : "4px",
            fontSize : "16px",
            backgroundColor : "white",
            backgroundPosition: "10px 10px",
            backgroundRepeat: "no-repeat",
            padding: "12px 20px 12px 40px",
            transition: "width 0.4s ease-in-out",
            backgroundImage : `url(${IconSearch})`,
            float : "right"

          }

        return(
          <div style = {{overflowY:"auto", width : "100%"}}>

          
            <Container>
            
              <div style = {{width : "100%", display : "flex", justifyContent : "space-between", padding : "15px 0"}}>

                <div style = {{padding : 10}}>recibir productos del pedido <b>#{PedidoId}</b></div>

                <div>
                  <input id="InputSearchProductoPedido" type="text" placeholder="Buscar.." style = {InputSearch}></input>
                </div>

              </div>

               


                <table style = {table} cellpadding="0" cellspacing="0" width="100%" rules="none">
                <thead>
                  <tr >
                    <th style = {th}>Producto</th>
                    <th style = {th}>Descripcin</th>
                    <th style = {th}>Cantidad <br/>Solicitada</th>
                    <th style = {th}>Cantidad <br/> Pendiente</th>
                    <th style = {th}>Cantidad <br/> a recibir</th>
                    <th style = {th}>Caducidad</th>
                    <th style = {th}>No. de lote</th>
                  </tr>
                </thead>

              <tbody id="myTable">
                {ProductosSeleccionados.map((res, i) =>{
    
                return(
                  <tr style = {tr}>
                    <td style = {td}>{res.ProductoCodigoBarra} </td>
                    <td style = {td}>{res.ProductoNombre} </td>
                    <td style = {td}>{res.PedidoProductosCantidad} </td>
                    <td style = {td}>{res.PedidoProductosCantidadPendiente} </td>
        
        

                    {/* Input para introducir las cantidades que entran */}
                    <td style = {td}>
                      <input name = {i}
                      // defaultValue = {res.ProductoCantidadRecibida} //{res.PedidoProductosCantidad}
                      value = {res.PedidoProductosCantidadPendiente > 0 ? res.ProductoCantidadRecibida : 0}//{res.ProductoCantidadRecibida}
                      onChange = {this.GetCantidad}
                      type = "number"
                      disabled = {res.PedidoProductosCantidadPendiente > 0 ? false : true}
                      style = {{textAlign:"center"}}
                      >

                      </input>


                    </td>


        {/* Input para introducir la fecha de venimiento */}
        <td style = {td}>
            <input 
              type="date" 
              id="FechaCaducidad" 
              name={i}
              disabled = {res.PedidoProductosCantidadPendiente > 0 ? false : true}
              onChange = {this.GetFechaCaducidad}
              value = {res.PedidoProductosCantidadPendiente > 0 ? res.PedidoProductosFechaCaducidad : ""}
              
            >


       </input>
       </td>

       {/* Input para introducir el no. de lote */}
       <td style = {td}>
            <input 
              id="Lote" 
              name={i}
              disabled = {res.PedidoProductosCantidadPendiente > 0 ? false : true}
              onChange = {this.GetLote}
              value = {res.PedidoProductosCantidadPendiente > 0 ? res.ProductoLote : ""}
            >


       </input>
       </td>

        
      </tr>
    )
  })}
  </tbody>
 
    </table>

              <ButtonGuardar onClick = {this.RecibirProductos}><SaveIcon/></ButtonGuardar>

                {/* seccion de alertas */}
                <div>
                <div className = "AlertDiv">
                  <Collapse in={OpenSucces}>
                    <Alert severity="success"
                      action={
                      <IconButton
                      aria-label="close"
                      color="inherit"
                      size="small"
                      onClick={() => {
                        this.setState({OpenSucces : false})
                      }}
                      >
                      <CloseIcon fontSize="inherit" />
                      </IconButton>
                      }
                      >

                      <AlertTitle><b> Aviso </b></AlertTitle>

                      {MensajeRest}
                    
                    </Alert>

                  </Collapse>


                   {/* Alerta de error  */}
        <Collapse in={OpenError}>
           <Alert severity="error"
           action={
             <IconButton
             aria-label="close"
             color="inherit"
             size="small"
             onClick={() => {
            this.setState({OpenError : false})
               }
             }
             >
       <CloseIcon fontSize="inherit" />
       </IconButton>
       }
       >
          <AlertTitle><b> Error </b></AlertTitle>
           {MensajeRest}
       </Alert>

                  </Collapse>


                </div>
              </div>

            </Container>

          <FixedPluginPedido
          // handleImageClick={handleImageClick}
          // handleColorClick={handleColorClick}
          // bgColor={color}
          // bgImage={image}
           handleFixedClick={this.handleFixedClick}
           fixedClasses={fixedClasses}
           handleCantidadCopiar = {this.getCantidadCopiar}
           handleCaducidadCopiar = {this.getFechaCaducidadCopiar}
           handleLoteCopiar = {this.getLoteCopiar}
          />
           
            </div>
        )
    }
}
)

export default RecibirProductosPedido