import React, { useState, useEffect } from 'react'
import MaterialTable from 'material-table';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import SaveIcon from '@material-ui/icons/Save';
import ipInfoLab from './../../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import Collapse from '@material-ui/core/Collapse';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import SpeedDial from '@material-ui/lab/SpeedDial';
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon';
import SpeedDialAction from '@material-ui/lab/SpeedDialAction';
import CheckIcon from '@material-ui/icons/Check';
import SendIcon from '@material-ui/icons/Send';
import Avatar from '@material-ui/core/Avatar';
import useToken from './../../Login/useToken';
import {GetPermisosAccionPantallaPerfil} from './../../../containers/Functions/GetPermisosAccionPantallaPerfil';



const useStyles = makeStyles((theme) => ({

    ButtonStyle: {
      margin: theme.spacing(1),
      backgroundColor: '#ecf6fd',
      border: 0,
      fontWeight: 'bold',
      color: '#2797ef',
      textTransform: 'none'
    },

    root: {
      height: 0,
      transform: 'translateZ(0px)',
      flexGrow: 1,
      position : 'fixed',
      right : -10,
      bottom : -10
    },
    speedDial: {
      position: 'absolute',
      bottom: theme.spacing(2),
      right: theme.spacing(2),
    },

  }));




  const ButtonGuardar = withStyles((theme) => ({
    root: {

      color: "white",
      backgroundColor: "#15b765",
      '&:hover': {
        backgroundColor: "#0a86d4",
      },
      borderRadius:100,
      // padding:"10px",
      height:60,
      boxShadow: "0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19)",
      bottom:10,
      right:10,
      position:"fixed",
      zIndex:100
    },
  }))(Button);

const MaterialTableProductosPedido = React.memo((props) =>
    {

      const { ProductosSeleccionados, Mode, PedidoId, AlmacenId, PedidoEstatus} = props;
      const [DataProductosSeleccionados, setDataProductosSeleccionados] = useState(ProductosSeleccionados)
     
      const classes = useStyles();
      const [openActions, setOpenActions] = React.useState(false);
      const [hiddenButtonActions, setHiddenButtonActions] = React.useState(PedidoEstatus === 4 ? true : false);
        
      const [data, setData] = useState(ProductosSeleccionados)
      const [ValueMensajeRest, setMensajeRest] = useState('');
      const [ValueExisteError, setExisteError] = useState(0);
      const [openSucces, setOpenSucces] = React.useState(false);
      const [openError, setOpenError] = React.useState(false);

      //Datos del usuario logueado
      const {TokenSdtUsuario} = useToken();
      const {UsuarioId, PerfilId} =  TokenSdtUsuario

      //variables donde se guardan las acciones permitidas dentro de la pantalla
      const [PermisoAUTORIZAPEDIDO, setPermisoAUTORIZAPEDIDO] = useState(false);
      
      //Acciones para ejecutar en el pedido
      const [actions, setactions] = useState([])
    
    useEffect(() => {
      const CantidadBackOrder = data.map(response => {
        const {BackOrder} = response
        return{...response, CantidadBackOrder :  <Avatar style = {{backgroundColor : "orange"}}>{BackOrder}</Avatar>}
      })

      setData(CantidadBackOrder)

      //Servicio para recuperar las acciones permitidas del usuario [INICIO]

      const FiltersPermisos = {
        PerfilId : PerfilId,
        OpcionId : 0,
        OpcionUrl : '/CatalogoPedidos'
      }

      GetPermisosAccionPantallaPerfil(FiltersPermisos)
      .then((response) => {
        let permisoAutorizar = false;
        let permisoCancelar = false; 
        let permisoSolicitar = false;

        const {SDTPerfilUsuarioOpcion} = response.data;

        SDTPerfilUsuarioOpcion.Accion.map((accion) => {
          const {AccionId, AccionAcceso} = accion

          switch (AccionId) {
            case "AUTORIZAPEDIDO":
              permisoAutorizar = AccionAcceso
              break;

            case "CANCELAPEDIDO":
              permisoCancelar = AccionAcceso
              break;

            case "SOLICITAPEDIDO":
              permisoSolicitar = AccionAcceso
              break;

              

            default:
              break;
          }
        })

        //Acciones permitidas para hacer al pedido
        
        let dataactions = []

        if(PedidoEstatus === 1 || PedidoEstatus === 2 || Mode === 'INS')
        {
          dataactions = [{ icon: <SaveIcon />, name: 'Guardar', action: GuardarPedido}]
        }
        
        if(permisoAutorizar && PedidoEstatus === 1)
        {
          dataactions = [...dataactions,  { icon: <CheckIcon />, name: 'Autorizar', action : AutorizarPedido }]
        }

        if(permisoCancelar && (PedidoEstatus === 1 || PedidoEstatus === 2))
        {
          dataactions = [...dataactions,  { icon: <CloseIcon />, name: 'Cancelar', action : CancelarPedido }]
        }

        if(permisoSolicitar && PedidoEstatus === 2)
        {
          dataactions = [...dataactions, { icon: <SendIcon />, name: 'Solicitar', action : SolicitarPedido }]
        }

        setactions(dataactions)
      })

      //Servicio para recuperar las acciones permitidas del usuario [FIN]

    


    }, [DataProductosSeleccionados])

    function AgrearNuevosProductos(){
        props.parentCallBackShowProductos(true)
        props.parentCallbackProductosSeleccionados(data)
    }

    function GuardarPedido(){
      
         const DataPedido = {
             // PedidoObservaciones : valuePedidoObservaciones,
             Modo : Mode,
             PedidoId : PedidoId,
             AlmacenId : AlmacenId,
             data : data,
             UsuarioId : UsuarioId
         }

         const GuardarPedido = new Promise((resolve, reject) => {

             const urlRest = ipInfoLab + '/rest/GuardarPedido'
             console.log(DataPedido)
             axios.post(urlRest, DataPedido).then(response => {
                 const MensajeRest = response.data.MensajeRest
                 const ExisteError = response.data.ExisteError

                 setExisteError(ExisteError)
                 setMensajeRest(MensajeRest)


         if(ExisteError == 0)
          {
             setOpenActions(false);
             setOpenSucces(true);
             setOpenError(false);
             setHiddenButtonActions(true)

             setTimeout(
               function()
               {
               setOpenSucces(false)
               window.history.back();
               }, 3000
             );
           }
          else
          {
            setOpenError(true);
            setOpenSucces(false);

            setTimeout(
              function()
              {
                setOpenError(false)
              }, 6000
            );
          }

             })
         });

    }

    function AutorizarPedido(){
      setOpenActions(false);
      const DataStatus = {
        UsuarioId : UsuarioId,
        PedidoId : PedidoId,
        PedidoEstatus : 2 //Estatus de autorizado
      }


      const GuardarPedido = new Promise((resolve, reject) => {
        const urlRest = ipInfoLab + '/rest/CambiarEstatusPedido'

        axios.post(urlRest, DataStatus)
        .then(response => {
          
          setOpenActions(false);
          setOpenSucces(true)
          setMensajeRest("Se autoriz el pedido")
          setHiddenButtonActions(true)

          setTimeout(
            function()
            {
            setOpenSucces(false)
            window.history.back();
            }, 3000
          );

        })

        .catch(reason => {
          console.log(reason)
        })
      })
    }


    function SolicitarPedido(){
      setOpenActions(false);
      const DataStatus = {
        UsuarioId : UsuarioId,
        PedidoId : PedidoId,
        PedidoEstatus : 3 //Estatus de solicitado
      }

      const GuardarPedido = new Promise((resolve, reject) => {
        const urlRest = ipInfoLab + '/rest/CambiarEstatusPedido'

        axios.post(urlRest, DataStatus)
        .then(response => {
          
          setOpenActions(false);
          setOpenSucces(true)
          setMensajeRest("Se solicit el pedido")
          setHiddenButtonActions(true)

          setTimeout(
            function()
            {
            setOpenSucces(false)
            window.history.back();
            }, 3000
          );

        })

        .catch(reason => {
          console.log(reason)
        })
      })
    }

    const CancelarPedido  = () => {
      setOpenActions(false);

      const DataStatus = {
        UsuarioId : UsuarioId,
        PedidoId : PedidoId,
        PedidoEstatus : 5 //Estatus de cancelado
      }

      const GuardarPedido = new Promise((resolve, reject) => {
        const urlRest = ipInfoLab + '/rest/CambiarEstatusPedido'

        axios.post(urlRest, DataStatus)
        .then(response => {
          
          setOpenActions(false);
          setOpenSucces(true)
          setMensajeRest("Se cancel el pedido")
          setHiddenButtonActions(true)

          setTimeout(
            function()
            {
            setOpenSucces(false)
            window.history.back();
            }, 3000
          );

        })

        .catch(reason => {
          console.log(reason)
        })
      })
    } 


    const handleVisibility = () => {
      setHiddenButtonActions((prevHidden) => !prevHidden);
    };

    const handleOpen = () => {
      setOpenActions(true);
    };

    function handleClose(e){
      setOpenActions(false);
    };

    return(
    <div>
        <MaterialTable
            title={
                <>Productos
               {Mode != 'DSP' &&
               <Button
                variant="outlined"
                color="primary"
                startIcon={<AddIcon></AddIcon>}
                className={classes.ButtonStyle}
                onClick = {AgrearNuevosProductos}
                >
                    Agregar productos 
                </Button>
            }

            
               </>
            }

            columns = {[
                { title: 'Producto', field: 'ProductoCodigoBarra', editable: 'never'},
                { title: 'Descripcin', field: 'ProductoNombre', editable: 'never' },
                { title: 'Proveedor', field: 'ProveedorNombre', editable: 'never' },
                { title: 'Cantidad', field: 'PedidoProductosCantidad', type : "numeric",
                  headerStyle: {
                    textAlign : "center"
                  },
                  cellStyle: {
                   textAlign : "center",
                  },
                },
                { title: 'BackOrder (solicitado)', field: 'BackOrder', editable: 'never',
                   headerStyle: {
                     textAlign : "center"
                   },
                   cellStyle: {
                     textAlign : "center"
                   ,
                   }
                 },

                { title: 'Unidad de compra', field: 'ProductoUnidadMedidaNombreCompra', editable: 'never' },
                { title: 'Unidad de consumo', field: 'ProductoUnidadMedidaNombreConsumo', editable: 'never' },
                


              ]}

            data= {data}

            localization={{

              body : {
                deleteTooltip :  Mode != 'DSP' ?  'Elimiar producto del pedido' : 'No se puede eliminar',//'Elimiar producto del pedido',

                editRow : {
                  deleteText : 'Desea eliminar el producto del pedido?',
                  cancelTooltip : 'Cancelar',
                  saveTooltip : 'Aceptar'
                },

                editTooltip : Mode != 'DSP' ?  'Modificar cantidad del producto' : 'No se puede modificar',//'Modificar cantidad del producto',
              },

              toolbar: {
                searchPlaceholder: 'Buscar producto',

              },

              pagination: {
                labelDisplayedRows: '{from}-{to} de {count}',
                labelRowsSelect: 'Productos por pgina',
            },
          }}

            editable={{
              isEditable: rowData => Mode != 'DSP',
              isDeletable: rowData => Mode != 'DSP',

                onRowUpdate: (newData, oldData) =>

                  new Promise((resolve, reject) => {
                    
                     const dataUpdate = [...data];
                     const index = oldData.tableData.id;
                     dataUpdate[index] = newData;
                     setDataProductosSeleccionados([...dataUpdate])
                     setData([...dataUpdate]);
                    
                    

                     resolve();
                    
                    //  setTimeout(() => {
                    //    const dataUpdate = [...data];
                    //    const index = oldData.tableData.id;
                    //    dataUpdate[index] = newData;
                    //    setDataProductosSeleccionados([...dataUpdate])
                    //    setData([...dataUpdate]);
                       

                    //    resolve();
                    //  }, 1000)
                  }),

                onRowDelete: oldData =>
                  new Promise((resolve, reject) => {
                    setTimeout(() => {
                      const dataDelete = [...data];
                      const index = oldData.tableData.id;
                      dataDelete.splice(index, 1);
                      setData([...dataDelete]);
                      setDataProductosSeleccionados([...dataDelete])

                      resolve();
                    }, 1000)
                  }),
                  
              }}

              options = {{
                search : true,
                draggable : false,
              }}





              // components={{
              //   Actions: props => (
              //     <div>
              //       <IconButton
              //         onClick={() =>
              //           alert("clic 1")
              //         }
              //       >
              //         <IconButton />
              //       </IconButton>
              //       <IconButton
              //         onClick={() =>
              //           alert("clic 2")
              //         }
              //       >
              //         <IconButton />
              //       </IconButton>
              //     </div>
              //   )
              // }}

            />

            {/* Primer boton que se realizo para guardar */}
            {/* <ButtonGuardar onClick = {GuardarPedido}><SaveIcon/></ButtonGuardar> */}



                {/* seccion de alertas */}
       <div>
         <div className = "AlertDiv">
           {/* Alerta de succes  */}
           <Collapse in={openSucces}>
             <Alert severity="success"
               action={
                 <IconButton
                 aria-label="close"
                 color="inherit"
                 size="small"
                 onClick={() => {
                 setOpenSucces(false);
                 }}
                 >
                 <CloseIcon fontSize="inherit" />
                 </IconButton>
               }
             >

                 <AlertTitle><b> Aviso </b></AlertTitle>

                 {ValueMensajeRest}
             </Alert>
           </Collapse>

        {/* Alerta de error  */}
        <Collapse in={openError}>
           <Alert severity="error"
           action={
             <IconButton
             aria-label="close"
             color="inherit"
             size="small"
             onClick={() => {
             setOpenError(false);
               }
             }
             >
       <CloseIcon fontSize="inherit" />
       </IconButton>
       }
       >
          <AlertTitle><b> Error </b></AlertTitle>
           {ValueMensajeRest}
       </Alert>

</Collapse>

         </div>
       </div>


     {/* Boton de acciones [INICIO] */}

     <div className={classes.root}>
      <SpeedDial
        ariaLabel="SpeedDial openIcon example"
        className={classes.speedDial}
        hidden={hiddenButtonActions}
        icon={<SpeedDialIcon openIcon={<CloseIcon />} />}
        onClose={handleClose}
        onOpen={handleOpen}
        open={openActions}
      >

        {/* <SpeedDialAction
            key="Guardar"
            icon= {<SaveIcon />}
            tooltipTitle="Guardar"
            hidden={true}
            onClick={GuardarPedido}
        />

        <SpeedDialAction
            key="Autorizar"
            icon= {<CheckIcon />}
            tooltipTitle="Autorizar"
            onClick={AutorizarPedido}
        />

        <SpeedDialAction
            key="Cancelar"
            icon= {<CloseIcon />}
            tooltipTitle="Cancelar"
            onClick={CancelarPedido}
        />

        <SpeedDialAction
            key="Imprimir"
            icon= {<PrintIcon />}
            tooltipTitle="Imprimir"
            onClick={handleClose}
        />

        <SpeedDialAction
            key="Solicitar"
            icon= {<SendIcon />}
            tooltipTitle="Solicitar"
            onClick={SolicitarPedido}
        /> */}

        {actions.map((action) => (
          <SpeedDialAction
            key={action.name}
            icon={action.icon}
            tooltipTitle={action.name}
            onClick={action.action}
          />
        ))}
        
      </SpeedDial>
    </div>
     {/* Boton de acciones [FIN] */}


    </div>)
    }
)

export default MaterialTableProductosPedido;