import { useState } from 'react';

export default function useToken() {

  const getToken = () => {
    
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken
  };


  const [token, setToken] = useState(getToken());


  const saveToken = userToken => {
    localStorage.setItem('token', JSON.stringify(userToken));
    setToken(userToken);
    
  };


  const getSdtUsuario = () => {
    const tokenString = localStorage.getItem('SdtUsuario')
    const SdtUsuarioToken = JSON.parse(tokenString);
    return SdtUsuarioToken 
}

const [TokenSdtUsuario, setTokenSdtUsuario] = useState(getSdtUsuario())

const SaveTokenSdtUsuario = SdtUsuarioToken => {
    localStorage.setItem('SdtUsuario', JSON.stringify(SdtUsuarioToken))
    setTokenSdtUsuario(SdtUsuarioToken)
}


  return {
    setToken: saveToken,
    token,
    setTokenSdtUsuario : SaveTokenSdtUsuario,
    TokenSdtUsuario
  }

}