import React from 'react'
import styled from 'styled-components';
import { getPostsCatalogoLog } from '../utils/RestLog';
import MaterialTableLog from 'material-table';

const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;

const CatalogoLog = React.memo (class CatalogoLog extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            postLog : []
        }
    }

    async  componentDidMount() {
        const LogFilters = {
            LogId : 0
        }

        getPostsCatalogoLog(LogFilters)
        .then((res) => {
            const {SDTLog} = res.data
            this.setState({postLog : SDTLog})
        })
    }
    
    render()
    {
        const {postLog} = this.state

        return(
            <Container>
                <div>
                    <MaterialTableLog
                    title = "Log"
                    columns = {[
                        {title : "#", field : "LogId"},
                        {title : "Usuario", field : "UsuarioId"},
                        {title : "Fecha / Hora", field : "LogFechaHora"},
                        {title : "Pantalla", field : "LogPantalla"},
                        {title : "Accin", field : "LogAccion"},
                        {title : "Detalle", field : "LogDescripcion"},
                    ]}
                    data = {postLog}


                    localization={{
                        toolbar: {
                          searchPlaceholder: 'Buscar movimiento',
                          
                        },
                
                        pagination: {
                          labelDisplayedRows: '{from}-{to} de {count}',
                          labelRowsSelect: 'Movimientos por pgina',
                      },
                    }}
                    >

                    </MaterialTableLog>
                </div>
            </Container> 
        )
    }

}
)

export default CatalogoLog