import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTableGrupos from './../components/ViewRegistros/MaterialTableGrupos'
import { getPosts } from './../utils/RestGrupo';


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoGrupos extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }


  async  componentDidMount() {
        
    const GrupoFilters = {
        GrupoId : 0,
        GrupoNombre : "",
    }

    await getPosts(GrupoFilters)
    .then((res) => {
        this.setState({
            posts: res.data,
            loading : false,
        });

    });
};

 render() {

  const {posts} = this.state;

    return (
       <Container>
        
        <MaterialTableGrupos
         posts = {posts}
         />
      </Container>
 
    );
  }
}
export default CatalogoGrupos;