import React from 'react';
import styled from 'styled-components';
import {getPostsProductoHistorico} from './../../utils/RestProductoHistorico'
import MaterialTableProductoHistorico from './MaterialTableProductoHistorico'

const Container = styled.div`
margin: 1%;
width:100%;
`;

class ProductoHistorico extends React.Component
{
    constructor(props){
        super(props);
      
        this.state =
        {
            posts: [],
            ProductoId : props.location.state.id,
            ProductoCodigoBarra : props.location.state.id,
        }
       
    }

    async componentDidMount() {

        const {ProductoId} = this.state

        const ProductoHistoricoFiltros = {
            ProductoId : ProductoId,
        }

        await getPostsProductoHistorico(ProductoHistoricoFiltros)
        .then((res) => {
            this.setState({
                posts: res.data,
            })
        })
        .catch(reason => {
            console.log(reason)
        })
    }

    render(){
        const {posts, ProductoCodigoBarra} = this.state;

        return(
 
                <Container>
                    <MaterialTableProductoHistorico
                    posts = {posts}
                    ProductoCodigoBarra = {ProductoCodigoBarra}
                    ></MaterialTableProductoHistorico>
                </Container>

        )
    }
}

export default ProductoHistorico;