import React, { useState } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import MaterialTable from 'material-table';
import Button from '@material-ui/core/Button';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogSalida from '@material-ui/core/Dialog';
import DialogEntradaDeUnProducto from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
// import DialogTitle from '@material-ui/core/DialogTitle';
import ipInfoLab from './../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import ComboBoxProveedor from './../../containers/ComboBox-Data/ComboBoxProveedor'
import ComboBoxMotivoMovimiento from './../../containers/ComboBox-Data/ComboBoxMotivoMovimiento'
import { getPosts } from './../../utils/RestKardex';
import ViewDetailMovimientoProducto from './Details/ViewDetailsMovimientoProducto'
import NewEntradaMovimiento from './../NewRegistros/NewEntradaMovimiento'
import AlertMessage from './../Messages/AlertMessage'
import Collapse from '@material-ui/core/Collapse';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import deLocale from "date-fns/locale/es";
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import Typography from '@material-ui/core/Typography';
import ModalEntradaSalidaProducto from './Details/Modal/ModalEntradaSalidaProducto'
import useToken from './../Login/useToken';
import {GetPermisosAccionPantallaPerfil} from './../../containers/Functions/GetPermisosAccionPantallaPerfil';



const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
    backgroundColor:"#22c488",
    borderColor: "#22c488",
    color:"#FFFFFF"
  },
  

  table: {
    minWidth: 700,
  },

  margin: {
    margin: theme.spacing(1),
    backgroundColor: '#ecf6fd',
    border: 0,
    fontWeight: 'bold',
    color: '#2797ef',
  },



}));

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const MaterialTableKardex = React.memo((props) => {
  const {posts} = props; //Recupera los productos del rest

  const data = props.posts
  const {AlmacenId} = props;
  const {Movimiento} = props //Recupera los movimientos de los productos
  const [statePopUp, setStatePopUp] = useState(false)
  const [openEntrada, setOpenEntrada] = React.useState(false);
  const [openSalida, setOpenSalida] = React.useState(false);
  const [scroll, setScroll] = React.useState('paper');
  const [openSucces, setOpenSucces] = React.useState(false);
  const [openError, setOpenError] = React.useState(false);

  const [valueDataTable, setValueDataTeble] = React.useState({posts})//[valueDataTable, setValueDataTeble] = useState([posts])
  const [open, setOpen] = React.useState(true);
  const [disablesButtonSave, setDisableButtonSave] = React.useState(false);
  const [OpenModalEntradaUnProducto, setOpenModalEntradaUnProducto] = React.useState(false)
  
  /*Date picker*/
  const [selectedDateCaducidad, setSelectedDateCaducidad] = React.useState(null);
  const [selectedDateKardexFechaMovimientoFisico, setSelectedDateKardexFechaMovimientoFisico] = React.useState(Date);
  const [valueMotivoMovimientoTipo, setvalueMotivoMovimientoTipo] = useState('');

  //Datos del usuario logueado
  const {TokenSdtUsuario} = useToken();
  const {UsuarioId, PerfilId} =  TokenSdtUsuario

  //Variables donde se guardan las acciones permitidas dentro de la pantalla
  const [PermisoGENERAENTRADA, setPermisoGENERAENTRADA] = useState(false)
  const [PermisoGENERASALIDA, setPermisoGENERASALIDA] = useState(false)
  const [PermisoGENERAENTRADARAPIDA, setPermisoGENERAENTRADARAPIDA] = useState(false)
  const [PermisoGENERASALIDARAPIDA, setPermisoGENERASALIDARAPIDA] = useState(false)

  //Eventos
  const handleDateChangeCaducidad = (date) => {
    setSelectedDateCaducidad(date);
    setValueCaducidad(date);
  };



  const handleDateChangeKardexFechaMovimientoFisico = (date) => {
    setSelectedDateKardexFechaMovimientoFisico(date);
    setValueKardexFechaMovimientoFisico(date);

  };


  const classes = useStyles();
  


  /* Pop up scroll [INICIO]*/
  const handleClickOpen = (scrollType) => () => {
    setOpenEntrada(true);
    setScroll(scrollType);

    //se lipian las avriables al abrir el modal para que no haya residuos
    setDisableButtonSave(false);
    setSelectedDateCaducidad(null);
    setValueCaducidad(null);
    setValueCantidad(null);
    setValueCodigoBarra(null);
    setValueMotivoMovimiento(null);
    setValueObservaciones(null);
    setValueProveedor(null);
    setValueUbicacion(null);
    setvalueMotivoMovimientoTipo('E')
  };

  const handleCloseEntrada = () => {
    setOpenEntrada(false);
    setOpenSucces(false);
    setOpenError(false);
    setvalueMotivoMovimientoTipo('')
  };

  const handleCloseSalida = () => {
    setOpenSalida(false);
    setOpenSucces(false);
    setOpenError(false);
  };

  const handelCloseModal = () => {
    setOpenModalEntradaUnProducto(false)
  }

  const descriptionElementRef = React.useRef(null);

  React.useEffect(() => {
    if (openEntrada) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }


    //Servicio para recuperar las acciones permitidas del usuario [INICIO]

    const FiltersPermisos = {
      PerfilId : PerfilId,
      OpcionId : 0,
      OpcionUrl : '/kardex'
    }

  
    GetPermisosAccionPantallaPerfil(FiltersPermisos)
    .then((response) => {
    
      const {SDTPerfilUsuarioOpcion} = response.data;

     
      SDTPerfilUsuarioOpcion.Accion.map((accion) => {
        const {AccionId, AccionAcceso} = accion

        switch (AccionId) {
          case "GENERAENTRADA":
              setPermisoGENERAENTRADA(AccionAcceso)
            break;
          
            case "GENERASALIDA":
              setPermisoGENERASALIDA(AccionAcceso)
            break;

            case "GENERAENTRADARAPIDA":
              setPermisoGENERAENTRADARAPIDA(AccionAcceso)
            break;

            case "GENERASALIDARAPIDA":
              setPermisoGENERASALIDARAPIDA(AccionAcceso)
            break;
        
          default:
            break;
        }
        
      })
      
      
    })
   

    //Servicio para recuperar los permisos del usuario [FIN]
  }, [openEntrada]);

  /* Pop up scroll [FIN]*/

  /* Variables de valores recuperados [INICIO] */

  const [valueProductoId, setValueProductoId] = useState(0)
  const [valueCodigobarra, setValueCodigoBarra] = useState('');
  const [valueCantidad, setValueCantidad] = useState(0);
  const [valueCaducidad, setValueCaducidad] = React.useState(null);
  const [valueKardexFechaMovimientoFisico, setValueKardexFechaMovimientoFisico] = React.useState(new Date());
  const [valueObservaciones, setValueObservaciones] = useState('');
  const [ValueMensajeRest, setMensajeRest] = useState('');
  const [ValueExisteError, setExisteError] = useState(0);
  const [ValueProveedor, setValueProveedor] = useState(0)
  const [ValueMotivoMovimiento, setValueMotivoMovimiento] = useState(0)
  const [valueUbicacion, setValueUbicacion] = useState('');
  const [valueMotivoMovimientoAbreviacion, setValueMotivoMovimientoAbreviacion] = useState('');
 // state = { messageProveedor: "" }
    

  /* Variables de valores recuperados [FIN] */

  /*  Modal PopUp [INICIO] */






  const handleClickOpenSalida = (scrollType) => () => {
    setOpenEntrada(true);
    setScroll(scrollType);

    //se lipian las avriables al abrir el modal para que no haya residuos
    setDisableButtonSave(false);
    setSelectedDateCaducidad(null);
    setValueCaducidad(null);
    setValueCantidad(null);
    setValueCodigoBarra(null);
    setValueMotivoMovimiento(null);
    setValueObservaciones(null);
    setValueProveedor(null);
    setValueUbicacion(null);
    setvalueMotivoMovimientoTipo('S')

    // setOpenSalida(true);
    // setDisableButtonSave(false)
    // setScroll(scrollType);
  };

   /*  Modal PopUp [FIN] */

/* Funciones para recuperar los valores introducidos [INICIO] */

  function getCodigoBarra(e){
    const ProductoCodigoBarra = e.target.value;
    setValueCodigoBarra(ProductoCodigoBarra);
  }

  function getCantidad(e){
    const ProductoKardexCantidad = e.target.value;
    setValueCantidad(ProductoKardexCantidad);
  }

  function getCaducidad(e){
    const ProductoKardexFechaCaducidad = e.target.value;
    setValueCaducidad(ProductoKardexFechaCaducidad);
  }

  function getObservaciones(e){
    const KardexObservaciones = e.target.value
    setValueObservaciones(KardexObservaciones)
  }

 
  function callbackProveedorFunction(ProveedorData) {
    const messageProveedor = ProveedorData
    setValueProveedor(messageProveedor)
  }

  function callbackMotivoMovimientoFunction(MotivoMovimientoData) {
    const messageMotivoMovimiento = MotivoMovimientoData
    setValueMotivoMovimiento(messageMotivoMovimiento)
  }

  function getUbicacion(e){
    const ProductoKardexUbicacion = e.target.value
    setValueUbicacion(ProductoKardexUbicacion)
  }

  function GenerarEntradaDeUnProducto(ProductoId)
  {

    setValueProductoId(ProductoId)
    setValueMotivoMovimientoAbreviacion('ER')//Entrada rpida
    setOpenModalEntradaUnProducto(true)
    setDisableButtonSave(null);
    setSelectedDateCaducidad(null);
    setValueCaducidad(null);
    setValueCantidad(null);
    setValueCodigoBarra(null);
    setValueMotivoMovimiento(null);
    setValueObservaciones(null);
    setValueProveedor(null);
    setValueUbicacion(null);
    setValueKardexFechaMovimientoFisico(new Date())
    setValueCaducidad(null);
  }

  function GenerarSalidaDeUnProducto(ProductoId)
  {
    setValueProductoId(ProductoId)
    setValueMotivoMovimientoAbreviacion('SR')//Entrada rpida
    setOpenModalEntradaUnProducto(true)
    setDisableButtonSave(null);
    setValueCaducidad(null);
    setValueCantidad(null);
    setValueCodigoBarra(null);
    setValueMotivoMovimiento(null);
    setValueObservaciones(null);
    setValueProveedor(null);
    setValueUbicacion(null);
    setValueKardexFechaMovimientoFisico(new Date())
    setValueCaducidad(null);
    setSelectedDateCaducidad(null)
  }

function GuardarMovimiento(){
  /*Armo la variable que se le enviara como parametro al proceso que guarda el movimiento */
   const DataNuevoMovimiento = ({
    UsuarioId : UsuarioId,
    SdtNuevoMovimiento : //Este es el nombre del parmetro en GX.
    {
    ProveedorKardexId : ValueProveedor,
    AlmacenKardexId : AlmacenId,
    KardexObservaciones : valueObservaciones,
    MotivoMovimientoId : ValueMotivoMovimiento,
    KardexFechaMovimientoFisico : valueKardexFechaMovimientoFisico,
    MotivoMovimientoAbreviacion : valueMotivoMovimientoAbreviacion,

    Productos : [{
      ProductoId : valueProductoId,//0,
      ProductoIdValue: valueProductoId,
      ProductoCodigoBarra: valueCodigobarra,
      ProductoKardexUbicacion : valueUbicacion,
      ProductoKardexCantidad :  valueCantidad,
      ProductoKardexFechaCaducidad : valueCaducidad,
      ProductoKardexFechaCaducidadtxt : valueCaducidad,
      PedidoId : 0,
      ProductoKardexLote : "",
      ProductoKardexDetalleId : 0,
      ProductoKardexId : 0
    }]
  }
  })

   const GuardarMovimiento = new Promise((resolve, reject) => {

    const urlRest = ipInfoLab + '/rest/GuardarMovimientoProducto'
        
    axios.post(urlRest, DataNuevoMovimiento)
    .then(response => {
      const MensajeRest = response.data.Mensaje
      const ExisteError = response.data.ExisteError

      setExisteError(ExisteError)
      setMensajeRest(MensajeRest)

      if(ExisteError == 0)
      {
        setOpenSucces(true)
        setDisableButtonSave(true)
        setOpenEntrada(false);
        setOpenSalida(false);
        setOpenModalEntradaUnProducto(false)
        setTimeout(
          function()
          {

            setOpenSucces(false)
            
          }, 3000
        );
        
      }

      else
      {
        
        setTimeout(
          function()
          {
            setOpenSucces(false);; 
            setOpenError(false);
            
          }, 9000
        );

        setOpenError(true);
        setOpenSucces(false);
      }
      
      props.parentCallback(ExisteError, MensajeRest)

      }).catch(reason => {
          console.log("la razon de error: " + reason);
        })

    });


    const KardexFilters = {
      AlmacenId : AlmacenId
    }
  }
/* Funciones para recuperar los valores introducidos [FIN] */


/*Funciones que recupera los valores de la entrada/Salida normal [INICIO] */
function callbackgetProductoCodigoBarra(CodigoBarraData){
  setValueCodigoBarra(CodigoBarraData)
}

function GetProveedorEntradaSalida(ProveedorData) {
  const messageProveedor = ProveedorData
  setValueProveedor(messageProveedor)
}

function callbackgetCantidad(CantidadData){
  setValueCantidad(CantidadData)
}

function callbackgetMotivoMovimiento(MotivoMovimientoData)
{
  setValueMotivoMovimiento(MotivoMovimientoData)
}

function callbackgetUbicacion(UbicacionData){
  setValueUbicacion(UbicacionData)
}

function callbackgetFechaCaducidad(date){
  setSelectedDateCaducidad(date);
  setValueCaducidad(date);
}

function callbackgeteKardexFechaMovimientoFisico(FechaMovimientoFisico){
  const KardexObservaciones = FechaMovimientoFisico
  setSelectedDateKardexFechaMovimientoFisico(KardexObservaciones);
  setValueKardexFechaMovimientoFisico(KardexObservaciones);
}

function callbackgetObservaciones(ObservacionesData){
  const KardexObservaciones = ObservacionesData
  setValueObservaciones(KardexObservaciones)
}

/*Funciones que recupera los valores de la entrada/Salida normal [FIN] */

  return (
    <div>

      <MaterialTable
    
       title={<>Inventario Actual 
       {PermisoGENERAENTRADA && <Button
       variant="outlined"
       color="primary"
       startIcon={<AddIcon></AddIcon>}
       onClick={handleClickOpen('paper')}
       className={classes.margin}
      
      >Generar Entrada</Button>}

       {PermisoGENERASALIDA && <Button
          // variant="contained"
          variant="outlined"
           color="primary"
          // className={classes.button}
           startIcon={<RemoveIcon>todo</RemoveIcon>}
           className={classes.margin}
           onClick={handleClickOpenSalida('paper')}
       >Generar Salida</Button>}
       </>}



      columns = {
      [
        { title: 'Producto', field: 'ProductoCodigoBarra', 
        cellStyle: {
          width: '10%',
         
        }, 
      },
        { title: 'Cdigo Alterno', field: 'ProductoCodigoProveedor',
        cellStyle: {
          width: '10%',
         
        }, 
      },
        
        { title: 'Descripcin', field: 'ProductoNombre',
          cellStyle: {
            width: '80%',
         
          }, 
        },
        { title: 'Total', field: 'CantidadExistente',
        cellStyle: {
          width: '5%',
         
        }, 
        },
        { title: 'Stock Mnimo', field: 'ProductoStockMinimo',
        
        cellStyle: {
          width: '5%',
         
        }, 
        },
        { title: 'Stock Mximo', field: 'ProductoStockMaximo',
        
        cellStyle: {
          width: '5%',
         
        }, 
        },
        { title: 'Punto Pedido', field: 'ProductoPuntoPedido',
        
        cellStyle: {
          width: '5%',
         
        }, 
        },
        { title: 'Punto Relleno', field: 'ProductoPuntoRelleno',

        cellStyle: {
          width: '5%',
         
        }, 
        },
        { title: 'Existencia', 
          field: 'color',
        },
       
      ]
    }
      data= {posts}


      actions={[
        {
          icon: PermisoGENERAENTRADARAPIDA ? 'add_circle' : '',//'add_circle',
          iconProps: PermisoGENERAENTRADARAPIDA ? { style: { fontSize: "24px", color: "#2797ef" } } : {},
          tooltip: PermisoGENERAENTRADARAPIDA ? 'Realizar Entrada' : '',
          onClick: PermisoGENERAENTRADARAPIDA ? (event, rowData) => GenerarEntradaDeUnProducto(rowData.ProductoId) : () => {},
          disabled : PermisoGENERAENTRADARAPIDA ? false : true
        },

        {
          icon: PermisoGENERASALIDARAPIDA ? 'remove_circle' : '',
          iconProps: PermisoGENERASALIDARAPIDA ? { style: { fontSize: "24px", color: "#FE4C25" } } : {},
          tooltip: PermisoGENERASALIDARAPIDA ? 'Realizar Salida' : '',
          onClick: PermisoGENERASALIDARAPIDA ? (event, rowData) => GenerarSalidaDeUnProducto(rowData.ProductoId) :() => {},
          disabled : PermisoGENERASALIDARAPIDA ? false : true
        },
      ]}

      

      options={{
       // paging:false,
         
        headerStyle: {
          backgroundColor: '#01579b',
          color: '#FFF'
        },

        rowStyle: {
          fontSize:16,
          height:10,
          width:20,
        },
       
        cellStyle: {
          width: 200,
          padding: '0',
        },

        pageSizeOptions:[5, 10, 20, 30],

        exportButton: true,
        exportFileName: "Inventario Actual",
        exportAllData: true ,

        actionsColumnIndex: 3 //Posicin de los botones de accin dentro de la grilla
        
      }}
      

      

      localization={{
        header: {
          actions: 'Entrada/Salida'
        },

        toolbar: {
          searchPlaceholder: 'Buscar producto',
          
        },

        pagination: {
          labelDisplayedRows: '{from}-{to} de {count}',
          labelRowsSelect: 'Productos por pgina',
      },
    }}
      
      detailPanel={rowData => {
        const dataDetail = rowData.Movimiento
        return (
          
              <ViewDetailMovimientoProducto
              rowData = {rowData}
              AlmacenId = {AlmacenId}
              dataDetail = {dataDetail}
              ProductoId = {rowData.ProductoId}
              ProductoCodigoBarra = {rowData.ProductoCodigoBarra}>
                

              </ViewDetailMovimientoProducto> 


        )
      }}

      onRowClick={(event, rowData, togglePanel) => togglePanel()}



      >
      </MaterialTable>





     

      {/* Nueva Entrada */}
      <div>

      <Dialog

        open={openEntrada}
        onClose={handleCloseEntrada}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
        fullWidth = {true}
        
        
      >
        
        <DialogTitle id="scroll-dialog-title"   
          style = {{backgroundColor:"#fd9610", color:"#ffffff"}}>

            {/* Titulo de entrada */}
            {valueMotivoMovimientoTipo == "E" && 
            <div>Entrada 
            <IconButton onClick={handleCloseEntrada} style = {{float: "right"}}>
            <CloseIcon />
            </IconButton></div>}

            {/* Titulo de Salida */}
            {valueMotivoMovimientoTipo == "S" && 
            <div>Salida 
            <IconButton onClick={handleCloseEntrada} style = {{float: "right"}}>
            <CloseIcon />
            </IconButton></div>}
            
           
          </DialogTitle>
          <DialogContent dividers={scroll === 'paper'} style={{paddingLeft:"24px", paddingTop:"0px", paddingBottom:"0px"}}>
          <DialogContentText
            id="scroll-dialog-description"
            ref={descriptionElementRef}
            tabIndex={-1}
          >
          
          <ModalEntradaSalidaProducto
          MotivoMovimientoTipo = {valueMotivoMovimientoTipo}
          callbackValueCodigoBarra = {callbackgetProductoCodigoBarra}
          callbackValueProveedor = {GetProveedorEntradaSalida}
          callbackValueCantidad = {callbackgetCantidad}
          callbackValueMotivoMovimiento = {callbackgetMotivoMovimiento}
          callbackValueUbicacion={callbackgetUbicacion}
          callbackValueFechaCaducidad = {callbackgetFechaCaducidad}
          callbackValueFechaMovimientoFisico = {callbackgeteKardexFechaMovimientoFisico}
          callbackValueObservaciones = {callbackgetObservaciones}
          
          >

          </ModalEntradaSalidaProducto>
          
          </DialogContentText>
        </DialogContent>

        <Button
              variant="contained"
              //color="primary"
              className={classes.button}
              onClick={GuardarMovimiento}//{newEntrada}
              disabled = {disablesButtonSave}
            >Aceptar</Button>
      </Dialog>
    </div>
    
    <DialogEntradaDeUnProducto
        open={OpenModalEntradaUnProducto}
        onClose={handelCloseModal}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
        fullWidth = {true}
        
      >
        
        <DialogTitle id="scroll-dialog-title"  onClose={handelCloseModal}>Movimiento Rpido</DialogTitle>
        <DialogContent dividers={scroll === 'paper'}>
          <DialogContentText
            id="scroll-dialog-description"
            ref={descriptionElementRef}
            tabIndex={-1}
          >

        <div>
        <TextField
          id="Cantidad"
          label="Cantidad"
          type="number"
          InputLabelProps={{
            shrink: true,
          }}
          variant="outlined"
          //fullWidth={true}
          onChange = {getCantidad}
          className="makeStyles-field"
          // fullWidth
        />


        {valueMotivoMovimientoAbreviacion == 'ER' &&  
        <MuiPickersUtilsProvider locale={deLocale} utils={DateFnsUtils} position="fixed" className="makeStyles-field" >

          <KeyboardDatePicker
          margin="normal"
          autoOk = {true}
          id="date-picker-dialog-Caducidad"
          label="Caducidad"
          format="dd/MM/yyyy"
          value={selectedDateCaducidad}
          onChange={handleDateChangeCaducidad}
          KeyboardButtonProps={{
          'aria-label': 'change date',

          }}
          />

        </MuiPickersUtilsProvider>}
       
        </div>     
          

        <TextField
          id="Observaciones"
          label="Observaciones"
          multiline
          rows={4}
          InputLabelProps={{
            shrink: true,
          }}
          variant="outlined"
          //fullWidth={true}
          onChange = {getObservaciones}
          className="makeStyles-field"
          fullWidth
        />


          </DialogContentText>
        </DialogContent>

        <Button
              variant="contained"
              color="primary"
              className={classes.button}
              onClick={GuardarMovimiento}//{newEntrada}
              disabled = {disablesButtonSave}
            >Aceptar</Button>
      
      </DialogEntradaDeUnProducto>



       {/* seccion de alertas */}
       <div className={classes.root}>
      <div className = "AlertDiv">

        {/* Alerta de succes  */}
        <Collapse in={openSucces}>
        <Alert severity="success"
        action={
        <IconButton
          aria-label="close"
          color="inherit"
          size="small"
          onClick={() => {
          setOpenSucces(false);
          }}
          >
        <CloseIcon fontSize="inherit" />
        </IconButton>
        }
        >
           <AlertTitle><b> Aviso </b></AlertTitle>
          {ValueMensajeRest}
        </Alert>
          
          

        </Collapse>

        {/* Alerta de error  */}

        <Collapse in={openError}>
          <Alert severity="error"
          action={
            <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={() => {
            setOpenError(false);
              }
            }
            >
          <CloseIcon fontSize="inherit" />
          </IconButton>
          }
        >
           
         <AlertTitle><b> Error </b></AlertTitle>
        {ValueMensajeRest}
      </Alert>
      
      </Collapse>

        </div>
      </div>

    </div>
  );
})

export default MaterialTableKardex