import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import styled from 'styled-components';
import Paper from '@material-ui/core/Paper';
import MaterialTableProductosPedido from './Details/MaterialTableProductosPedido';
import SeleccionarProductosPedido from './Details/SeleccionarProductosPedido';
import { getPostsPedido } from './../../utils/RestPedido';
import ComboBoxAlmacenLine from './../../containers/ComboBox-Data/ComboBoxAlmacenLine'
import FormControl from '@material-ui/core/FormControl';
import Chip from '@material-ui/core/Chip';
import {GetPermisosAccionPantallaPerfil} from './../../containers/Functions/GetPermisosAccionPantallaPerfil';




const Container = styled.div`
margin: 1%;
width:97%;
`;




const NewPedido = React.memo(class NewPedido extends React.Component{

  

  constructor(props) {
    super(props);

    this.state = {
      ShowSeleccionProductos : true,
      ProductosSeleccionados : [],
      Mode : props.location.state.Mode,
      PedidoId : props.location.state.id,
      AlmacenId : 0,
      PedidoEstatus : 0,
    }
  }

  async componentDidMount()
  {
    const {Mode, PedidoId} = this.state

    
    if(Mode === "UPD" || Mode === "DSP")
    {

      const PedidoFilters = {
        PedidoId : PedidoId,
        ProveedorId : 0
      }

      
       await getPostsPedido(PedidoFilters)
       .then((res) => {
         
         const {data} = res
         const {Productos, AlmacenPedidoId, PedidoEstatus} = data[0]

    

          this.setState({
               ProductosSeleccionados: Productos,
               AlmacenId : AlmacenPedidoId,
               PedidoEstatus : PedidoEstatus
           });

       });

      this.setState({ShowSeleccionProductos : false})

    }

    
  }

  callbackGetShowProductos = (DataShowProducto) => {

    this.setState({ShowSeleccionProductos : DataShowProducto})

  }

  callbackGetProductoSeleccionados = (DataProductosSeleccionados) => {

    this.setState({ProductosSeleccionados : DataProductosSeleccionados})

  }

  callbackAlmacenFunction = (AlmacenData) => {
    this.setState({AlmacenId : AlmacenData})
  }

render(){

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});




const {ShowSeleccionProductos, ProductosSeleccionados, Mode, PedidoId, AlmacenId, PedidoEstatus} = this.state




    return(
      <div style = {{overflowY:"auto", width : "100%"}}>
        <Container>
          <div style={{display:"flex", width:"100%", height : "100px"}}>

            {Mode === "INS" && (<Chip label="Nuevo Pedido" style = {{marginRight : "70%", fontSize : 20}}/>)}
            {Mode === "UPD" && (<Chip label={`Editar pedido # ${PedidoId}` } style = {{marginRight : "70%", fontSize : 20}} />)}
            {Mode === "DSP" && (<Chip label={`Detalle del pedido # ${PedidoId}` } style = {{marginRight : "70%", fontSize : 20}}/>)}

            <div id = "DivAlmacen" style = {{width:"20%", position:"relative"}}>
              <FormControl  variant="outlined" fullWidth = {true} >
        
                <ComboBoxAlmacenLine 
                parentCallback = {this.callbackAlmacenFunction}
                FAlmacenId = {AlmacenId}
                />
        
              </FormControl>
            </div>

          </div>


        <div className = "cardNewRegistro">

          
          <div className = {ShowSeleccionProductos ? "jss1 jss5 jss2" : " "}>

          {ShowSeleccionProductos && ( 
          <SeleccionarProductosPedido 
          parentCallBackShowProductos = {this.callbackGetShowProductos}
          parentCallbackProductosSeleccionados = {this.callbackGetProductoSeleccionados}
          ProductosSeleccionados = {ProductosSeleccionados}
          AlmacenId = {AlmacenId}
          
          />
          )} 
         
          {!ShowSeleccionProductos && (
          <MaterialTableProductosPedido
            ProductosSeleccionados = {ProductosSeleccionados}
            parentCallBackShowProductos = {this.callbackGetShowProductos}
            parentCallbackProductosSeleccionados = {this.callbackGetProductoSeleccionados}
            Mode = {Mode}
            PedidoId = {PedidoId}
            AlmacenId = {AlmacenId}
            PedidoEstatus = {PedidoEstatus}
          
          />
          )} 
          
          </div>
        </div>

      </Container>
      </div>
    )

  }

}
)

export default NewPedido