import React, {useState, useEffect} from 'react';
import MaterialTable from 'material-table';
import { makeStyles } from '@material-ui/core/styles';
import ButtonNew from '../Buttons/ButtonNewRegistro'//'././Buttons/ButtonNewRegistro';
import MenuPopupState from './../../MenuGrids/MenuMaterialTableCatalogos'
import useToken from './../Login/useToken';
import {GetPermisosAccionPantallaPerfil} from './../../containers/Functions/GetPermisosAccionPantallaPerfil';


const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  
    table: {
      minWidth: 700,
    },
  
    margin: {
      margin: theme.spacing(1),
      backgroundColor: '#ecf6fd',
      border: 0,
      fontWeight: 'bold',
      color: '#2797ef',
    },
  
  }));


const MaterialTableProducto = React.memo((props) =>
{

    const {posts} =  props;
    

    const classes = useStyles();

    const [dataMaterialTable, setdataMaterialTable] = useState([])

    //Se obtienen los datos del usuario logueado
    const {TokenSdtUsuario} = useToken();
    const {UsuarioId, PerfilId} =  TokenSdtUsuario

    //Variables donde se guardan las acciones permitidas dentro de la pantalla
    const [PermisoAGREGARPRODUCTO, setPermisoAGREGARPRODUCTO] = useState(false)
    const [PermisoMODIFICARPRODUCTO, setPermisoMODIFICARPRODUCTO] = useState(false)
    const [PermisoCONSULTARHISTORICO, setPermisoCONSULTARHISTORICO] = useState(false)
    


    useEffect (() => {
      //Servicio para recuperar las acciones permitidas del usuario [INICIO]
      const FiltersPermisos = {
        PerfilId : PerfilId,
        OpcionId : 0,
        OpcionUrl : '/CatalogoProducto'
      }

      const PermisosMenu =  GetPermisosAccionPantallaPerfil(FiltersPermisos)
      .then((response) => {

        return {response}

      })

      var getPermisos = Promise.resolve(PermisosMenu);
      getPermisos.then(function(value) {

      const {SDTPerfilUsuarioOpcion} =  value.response.data
      const {Accion} = SDTPerfilUsuarioOpcion


      
      const DataWithMenu = posts.map(Response => {
      const {ProductoId, ProductoCodigoBarra} = Response;
    
      


      //Se arman las opciones que tendr el men delmaterial table
      let OpcionesMenuMaterialTable = []
      var {permisoModificar, Permisohistorico} = false
      
      Accion.map((accion) => {
        const {AccionId, AccionAcceso} = accion

        switch (AccionId) {
          case "AGREGARPRODUCTO":
           setPermisoAGREGARPRODUCTO(AccionAcceso)
           break;

         case "CONSULTARHISTORICO":
          Permisohistorico = AccionAcceso
          setPermisoCONSULTARHISTORICO(AccionAcceso)
          
          break;

         case "MODIFICARPRODUCTO":
          permisoModificar = AccionAcceso
          setPermisoMODIFICARPRODUCTO(AccionAcceso)
          break;
        
         default:
           break;
        }

      })

      OpcionesMenuMaterialTable = [
        {
          pathname : "/ViewProducto",
          state : [{
            Mode : "DSP",
            id : ProductoId,
          }],
      
          TituloOpcion : "Visualizar"
        }
      ]

      if(permisoModificar)
      {
        let datosmenu = [...OpcionesMenuMaterialTable,
          {
            pathname : "/UpdateProducto",
             state : [{
               Mode : "UPD",
               id : ProductoId,
             }],
  
             TituloOpcion : "Modificar"
          }
         ]

         OpcionesMenuMaterialTable = datosmenu
      }

      if (Permisohistorico) {
        let datosmenu = [...OpcionesMenuMaterialTable,
          {
            pathname : "/ProductoHistorico",
             state : [{
               Mode : "DSP",
               id : ProductoId,
             }],
  
             TituloOpcion : "Consultar histrico"
          }
         ]

         OpcionesMenuMaterialTable = datosmenu
      }

      return{...Response, DataWithMenu: <MenuPopupState OpcionesMenuMaterialTable= {OpcionesMenuMaterialTable}> </MenuPopupState>}
    
    })
      //Servicio para recuperar las acciones permitidas del usuario [FIN]
    
      setdataMaterialTable(DataWithMenu)
    })

    }, [props])

    

    return(
        <div>

            <MaterialTable
                title={
                    <>Productos
                    {
                    PermisoAGREGARPRODUCTO && 
                    <ButtonNew
                    UrlButton = {"/NuevoProducto"}
                    TittleButton = {"Agregar Producto"}
                    Mode = "INS"
                    />
                    }

                   </>
                }

                columns = {[
                    { title: 'Producto', field: 'ProductoCodigoBarra',
                      cellStyle: {
                        width: '20%',
                     
                      },
                    },
                    { title: 'Cdigo Alterno', field: 'ProductoCodigoProveedor',
                    cellStyle: {
                      width: '20%',
                   
                    },
                   },
                    { title: 'Descripcin', field: 'ProductoNombre',
                    cellStyle: {
                      width: '100%',
                   
                    },
                    },
                    { title: 'Tipo', field: 'ProductoTipoDescripcion',
                    cellStyle: {
                      width: '10%',
                   
                    },
                   },
                    { title: 'Grupo', field: 'GrupoNombre',
                    cellStyle: {
                      width: '10%',
                   
                    },
                   },
                    { title: '', field: 'DataWithMenu',
                    cellStyle: {
                      width: '1%',
                   
                    },
                   },
                    
                   
                    
                    
                    
                    
                ]}

                data= {dataMaterialTable}//{posts}

                options={{
                      
                     headerStyle: {
                     backgroundColor: '#4FC3A1',
                     color: '#ffffff',
                     padding: 8,
                     position: 'sticky', 
                     top: 0
                     },
                     rowStyle: {
                     fontSize:16,
                     height:10,
                     width:20,
                    },
                    cellStyle: {
                     width: 200,
                     
                   },
                   
                }}

                localization={{
                    toolbar: {
                      searchPlaceholder: 'Buscar producto',
                      
                    },
            
                    pagination: {
                      labelDisplayedRows: '{from}-{to} de {count}',
                      labelRowsSelect: 'Productos por pgina',
                  },
                }}
            />
        </div>
    )

})

export default MaterialTableProducto