import React, { useEffect, useState } from 'react';
import Icon from "@material-ui/core/Icon";
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import TrendingDownIcon from '@material-ui/icons/TrendingDown';
import Store from "@material-ui/icons/Store";
import DateRange from "@material-ui/icons/DateRange";
import Accessibility from "@material-ui/icons/Accessibility";
import Update from "@material-ui/icons/Update";
import GridContainer from "./Grid/GridContainer.js";
import GridItem from "./Grid/GridItem.js";
import Table from "./Table/Table.js";
import Card from "./Card/Card.js";
import CardHeader from "./Card/CardHeader.js";
import CardIcon from "./Card/CardIcon.js";
import CardBody from "./Card/CardBody.js";
import CardFooter from "./Card/CardFooter.js";
import { makeStyles } from "@material-ui/core/styles";
import Danger from './Card/Typography/Danger.js'
import Warning from './Card/Typography/Warning'

import styles from "./Card/CardStyles/dashboardStyle.js";
import ipInfoLab from './../containers/Functions/GetIpInfolab';
import Axios from 'axios';
import { getPostsPedido } from '../utils/RestPedido';

const useStyles = makeStyles(styles);

function Home(){

        const classes = useStyles();

        const [TotalProductosStockMinimo, setTotalProductosStockMinimo] = useState(0);
        const [TotalEntradas, setTotalEntradas] = useState(0);
        const [TotalSalidas, setTotalSalidas] = useState(0);
        const [TotalPedidosPendientes, setTotalPedidosPendientes] = useState(0);
        const [PedidosPendientes, setPedidosPendientes] = useState([])
        const [HoraActual, setHoraActual] = useState(Date().toLocaleString('ES'))

        const urlRest = ipInfoLab + '/rest/DatosInicioStockInfoLab'
          
        const [tableData, settableData] = useState([])
        
        useEffect(() => {
        Axios.post(urlRest)
        .then(response => {
            const data = response.data

            const {TotalEntradas, TotalPedidosPendientes, TotalProductosStockMinimo, TotalSalidas} = data
            setTotalEntradas(TotalEntradas)
            setTotalPedidosPendientes(TotalPedidosPendientes)
            setTotalProductosStockMinimo(TotalProductosStockMinimo)
            setTotalSalidas(TotalSalidas)
            
        })

        const PedidoFilters = {
            PedidoId : 0
        }

        getPostsPedido(PedidoFilters)
        .then(response => {
            const data = response.data
            setPedidosPendientes(data)
           
        })

    },[false])






    var PedidosPorCompletar = []

    PedidosPendientes.map((Pedido, i) =>
   
    PedidosPorCompletar[i] = [Pedido.PedidoId, Pedido.PedidoFechaHora, Pedido.PedidoEstatus]
    )
            

        return(
            <div style = {{width:"100%", backgroundImage:'url("https://bioalimentar.com/newpage/wp-content/uploads/2018/12/LaboratorioHeader.jpg?id=4635&45dd30&45dd30")', backgroundRepeat:"no-repeat",  height: "100%", backgroundSize:"cover", 
             overflowX:"auto"}}>
                <div style = {{ padding:"15px"}}>

                

                <GridContainer>
                    <GridItem xs={12} sm={6} md={3}>
                        <Card>
                        <CardHeader color="warning" stats icon>
                        <CardIcon color="warning">
                            <TrendingDownIcon></TrendingDownIcon>
                        </CardIcon>

                        <p className={classes.cardCategory}>Productos en stock mnimo</p>
                        <h3 className={classes.cardTitle}>
                        {TotalProductosStockMinimo}
                        </h3>
                       

                        </CardHeader>

                            <CardFooter stats>
                                <div className={classes.stats}>
                                    <Danger>
                                        <Warning />
                                    </Danger>
                                    <a href="#pablo" onClick={e => e.preventDefault()}>
                                    Ver inventario
                                    </a>
                                </div>
                            </CardFooter>
                        </Card>
                    </GridItem>

                    <GridItem xs={12} sm={6} md={3}>
                        <Card>
                            <CardHeader color="success" stats icon>
                            <CardIcon color="success">
                            <ArrowDownwardIcon />
                            </CardIcon>
                            <p className={classes.cardCategory}>Entradas</p>
                            <h3 className={classes.cardTitle}>{TotalEntradas}</h3>
                            </CardHeader>
                            <CardFooter stats>
                            <div className={classes.stats}>
                                <DateRange /> 
                                Semana actual
                            </div>
                            </CardFooter>
                        </Card>
                    </GridItem>

                    <GridItem xs={12} sm={6} md={3}>
                        <Card>
                            <CardHeader color="success" stats icon>
                            <CardIcon color="success">
                            <ArrowUpwardIcon />
                            </CardIcon>
                            <p className={classes.cardCategory}>Salidas</p>
                            <h3 className={classes.cardTitle}>{TotalSalidas}</h3>
                            </CardHeader>
                            <CardFooter stats>
                            <div className={classes.stats}>
                                <DateRange /> 
                                Semana actual
                            </div>
                            </CardFooter>
                        </Card>
                    </GridItem>

                    <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="info" stats icon>
              <CardIcon color="info">
                <Accessibility />
              </CardIcon>
              <p className={classes.cardCategory}>Pedidos Pendientes</p>
              <h3 className={classes.cardTitle}>{TotalPedidosPendientes}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                <Update />
                {HoraActual}
              </div>
            </CardFooter>
          </Card>
        </GridItem>

                </GridContainer>

                <GridContainer>

                <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHeader color="warning">
              <h4 className={classes.cardTitleWhite}>Pedidos Pendientes</h4>
              <p className={classes.cardCategoryWhite}>
                Pedidos que an no se han recibido completamente
              </p>
            </CardHeader>
            <CardBody>
              <Table
                tableHeaderColor="warning"
                tableHead={["NO. Pedido", "Fecha", "Estatus"]}
           
                tableData={PedidosPorCompletar}
              />
            </CardBody>
          </Card>
        </GridItem>

                </GridContainer>
                </div>
            </div>
        )

}

export default Home;