import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTableSecciones from './../components/ViewRegistros/MaterialTableSecciones'
import { getPosts } from './../utils/RestSeccion';


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoSecciones extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }


  async  componentDidMount() {
        
    const SeccionFilters = {
        SeccionId : 0,
        SeccionNombre : "",
    }

    await getPosts(SeccionFilters)
    .then((res) => {
        this.setState({
            posts: res.data,
            loading : false,
        });

    });
};

 render() {

  const {posts} = this.state;

    return (
       <Container>
        
        <MaterialTableSecciones
         posts = {posts}
         />
      </Container>
 
    );
  }
}
export default CatalogoSecciones;