import React, { useEffect, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import styled from 'styled-components';
import ipInfoLab from '../../containers/Functions/GetIpInfolab';
import axios from 'axios';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import Collapse from '@material-ui/core/Collapse';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import { getPosts } from './../../utils/RestUnidadMedida';
import useToken from './../Login/useToken';


const useStyles = makeStyles((theme) => (
  {
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '100%',
      },
    },
  }));

const Container = styled.div`
margin: 1%;
width:100%;
`;


const CRUDUNIDADMEDIDA = (props) => {

    const {id, Mode} = props.location.state
    const UnidadMedidaId = id
    const classes = useStyles();
    const [valueUnidadMedida, setValueUnidadMedida] = useState(''); //UseState recupera el valor introducido
    const [ValueMensajeRest, setMensajeRest] = useState('');
    const [ValueExisteError, setExisteError] = useState(0);
    const [open, setOpen] = React.useState(false);
    const [DisabledCampos, setDisabledCampos] = React.useState(false);//si se entra en modo DSP no se podr modificar el registro
    const {TokenSdtUsuario} = useToken();
    const {UsuarioId} =  TokenSdtUsuario
    

    useEffect(() => {
      if(Mode == "UPD" || Mode == "DSP")
      {

        if (Mode === 'DSP') {
          setDisabledCampos(true)
        }

        const UnidadMedidaFilter = {
          UnidadMedidaId : UnidadMedidaId,
          UnidadMedidaNombre : ""
        }

        getPosts(UnidadMedidaFilter)
        .then((res) => {
          const data = res.data
          data.map(post => {
            const {UnidadMedidaNombre} = post
            setValueUnidadMedida(UnidadMedidaNombre)
          })

    })
      }

    },[false])

    //Por cada cambio en el textfield, se va guardando lo que el usuario va introduciendo en las funciones get
    function  getUnidadMedidaValue(e){
        const UnidadMedidaValue = e.target.value;
        setValueUnidadMedida(UnidadMedidaValue)
    }

    function SaveUnidadMedida()
    {
        const DataUnidadMedida = {
                    Modo : Mode,
                    UnidadMedidaId : UnidadMedidaId,
                    UnidadMedidaNombre :  valueUnidadMedida,
                    UsuarioId : UsuarioId
                    }

        const GuardarUnidadMedida = new Promise((resolve, reject) => {

          const urlRest = ipInfoLab + '/rest/GuardarUnidadMedida'
          
              
            axios.post(urlRest, DataUnidadMedida).then(response => {
              
              const MensajeRest = response.data.Mensaje
              const ExisteError = response.data.ExisteError
              setExisteError(ExisteError)
              setMensajeRest(MensajeRest)
                          
              if(ExisteError == 0)
              {
                           
                setTimeout(function(){ window.history.back(); }, 3000);

              }else
              {
                  setTimeout(function(){setOpen(false);; }, 6000);
                            
              }
                          
              }).catch(reason => {
                    console.log("la razon de error: " + reason);
              })
        });

        setOpen(true);
    }

    function ShowMessage()
    {
      if(ValueExisteError == 1)
      {

      
        return(
          <Collapse in={open}>
            <Alert severity="error"
              action={
                <IconButton
                  aria-label="close"
                  color="inherit"
                  size="small"
                  onClick={() => {
                    setOpen(false);
                  }}
                >
                  <CloseIcon fontSize="inherit" />
                </IconButton>
              }
            >
            <AlertTitle><b> Error </b></AlertTitle>
              {ValueMensajeRest}
            </Alert>
      
          </Collapse>

        )

      }
      else
      {
      
        return(
          <Collapse in={open}>
            <Alert severity="success"
              action={
                <IconButton
                  aria-label="close"
                  color="inherit"
                  size="small"
                  onClick={() => 
                    {
                      setOpen(false);
                    }
                  }
                >
                <CloseIcon fontSize="inherit" />
                </IconButton>
                }
            >
                <AlertTitle><b> Aviso </b></AlertTitle>
                  {ValueMensajeRest}
                </Alert>
        
          </Collapse>
      )
    }
    }

    return (
    

     <Container>

     
            {Mode == "INS" &&<h3>Nueva Unidad de Medida</h3>}
            {Mode == "UPD" &&<h3>Modificar Unidad de Medida</h3>}
            {Mode == "DSP" &&<h3>Visualizar Unidad de Medida</h3>}

            <div className = "cardNewRegistro">
                <div className = "jss1 jss5 jss2">
                <form className={classes.root} noValidate autoComplete="off">
                    <TextField  disabled  = {DisabledCampos} id="UnidadMedida" label="UnidadMedida" variant="outlined" onChange={getUnidadMedidaValue} value = {valueUnidadMedida}/> 
                </form>

                <button  disabled  = {DisabledCampos} type="button" className = {!DisabledCampos ? "btn btn-green-light btn-pill"  : "btn btn-gray-light btn-pill "} onClick = {SaveUnidadMedida} >Guardar</button>
                
                <div className = "AlertDiv">

                  <ShowMessage/>
                </div>
                
                </div>
            </div>
        
    </Container>

    );
}


export default CRUDUNIDADMEDIDA;
