import React, { useEffect, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import styled from 'styled-components';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import ipInfoLab from '../../containers/Functions/GetIpInfolab';
import Axios from 'axios';
import Collapse from '@material-ui/core/Collapse';
import { Alert, AlertTitle } from '@material-ui/lab';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { getPosts } from './../../utils/RestMotivoMovimiento';
import useToken from './../Login/useToken';


const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
          margin: theme.spacing(1),
          width: '100%',
        },
      },
    
}));

const Container = styled.div`
margin: 1%;
width:100%;
`;

const CRUDMOTIVOMOVIMIENTO = (props) => {

  const {id, Mode} = props.location.state
  const MotivoMovimientoId = id

  const classes = useStyles();

  const [valueMotivoMovimiento, setValueMotivoMovimiento] = useState(''); //UseState recupera el valor introducido
  const [valueMotivoMoviminetoTipo, setValueMotivoMovimientoTipo] = useState('');
  const [ValueMensajeRest, setMensajeRest] = useState('');
  const [ValueExisteError, setExisteError] = useState(0);
  const [openSucces, setOpenSucces] = React.useState(false);
  const [openError, setOpenError] = React.useState(false);
  const [DisabledCampos, setDisabledCampos] = React.useState(false);//si se entra en modo DSP no se podr modificar el registro
  const {TokenSdtUsuario} = useToken();
  const {UsuarioId} =  TokenSdtUsuario


  useEffect(() => {
    if(Mode == "UPD" || Mode == "DSP")
    {

      if (Mode === 'DSP') {
        setDisabledCampos(true)
      }

      const MotivoMovimientoFilters = {
        MotivoMovimientoId : MotivoMovimientoId,
        MotivoMovimientoNombre : "",
        MotivoMovimientoTipo:""
      }

      getPosts(MotivoMovimientoFilters)
      .then((res) => {
       const data = res.data

       data.map(post => {
         const {MotivoMovimientoNombre, MotivoMovimientoTipo} = post
         setValueMotivoMovimiento(MotivoMovimientoNombre)
         setValueMotivoMovimientoTipo(MotivoMovimientoTipo)
       })

      });
    }

  },[false])

  //Por cada cambio en el textfield, se va guardando lo que el usuario va introduciendo en las funciones get
  function  getMotivoMovimientoValue(e){
    const MotivoMovimientoValue = e.target.value;
    setValueMotivoMovimiento(MotivoMovimientoValue)
}

function getMotivoMovimientoTipoValue(e){
    const name = e.target.name;
    setState({
      ...state,
      [name]: e.target.value,
    });

    
    const MotivoMovimientoTipoValue = e.target.value;
    setValueMotivoMovimientoTipo(MotivoMovimientoTipoValue)
}

function SaveMotivoMovimiento()
{
    const DataMotivoMovimiento = {
      Modo : Mode,
      MotivoMovimientoId : MotivoMovimientoId,
      MotivoMovimientoNombre :  valueMotivoMovimiento,
      MotivoMovimientoTipo : valueMotivoMoviminetoTipo,
      UsuarioId : UsuarioId
    }
    
    const GuardarMotivoMovimiento = new Promise((resolve, reject) => {
      
      const urlRest = ipInfoLab + '/rest/GuardarMotivoMovimiento'

      Axios.post(urlRest, DataMotivoMovimiento).then(response => {

        const MensajeRest = response.data.Mensaje
        const ExisteError = response.data.ExisteError
        setExisteError(ExisteError)
        setMensajeRest(MensajeRest)

      if(ExisteError == 0)
      {
      setOpenSucces(true)
      // setDisableButtonSave(true)
      setTimeout(
        function()
        {
          setOpenSucces(false)
          window.history.back();
        }, 3000
      );
      
    }

       else
              {
                setTimeout(
                  function()
                  {
                    setOpenSucces(false);; 
                    setOpenError(false);
                    
                  }, 9000
                );
          
                setOpenError(true);
                setOpenSucces(false);
                            
              }
                          
              }).catch(reason => {
                    console.log("la razon de error: " + reason);

      });

    })       

}

  const [state, setState] = React.useState({
    age: '',
    name: 'hai',
  });

  const handleChange = (event) => {
    const name = event.target.name;
    setState({
      ...state,
      [name]: event.target.value,
    });
  };

  

  return (

<Container>

     

{Mode == "INS" &&<h3>Nuevo Motivo de movimiento</h3>}
{Mode == "UPD" &&<h3>Modificar Motivo de movimiento</h3>}
{Mode == "DSP" &&<h3>Visualizar Motivo de movimiento</h3>}

<div className = "cardNewRegistro">
    <div className = "jss1 jss5 jss2">
    <form className={classes.root} noValidate autoComplete="off">
        <TextField disabled  = {DisabledCampos} id="MotivoMovimiento" label="MotivoMovimiento" variant="outlined" onChange={getMotivoMovimientoValue} value = {valueMotivoMovimiento}/> 

        <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel  htmlFor="outlined-age-native-simple">Tipo</InputLabel>
        <Select
          native
          value={valueMotivoMoviminetoTipo}
          onChange={getMotivoMovimientoTipoValue}
          label="Age"
          inputProps={{
            name: 'age',
            id: 'outlined-age-native-simple',
          }}
          disabled  = {DisabledCampos}
        >
          <option aria-label="None" value="" />
          <option value={'E'}>Entrada</option>
          <option value={'S'}>Salida</option>
        </Select>
      </FormControl>
      
    </form>

    <button disabled  = {DisabledCampos} type="button"  className = {!DisabledCampos ? "btn btn-green-light btn-pill"  : "btn btn-gray-light btn-pill "} onClick = {SaveMotivoMovimiento} >Guardar</button>
    </div>
</div>



 {/* seccion de alertas */}
 <div className={classes.root}>
      <div className = "AlertDiv">

        {/* Alerta de succes  */}
        <Collapse in={openSucces}>
        <Alert severity="success"
        action={
        <IconButton
          aria-label="close"
          color="inherit"
          size="small"
          onClick={() => {
          setOpenSucces(false);
          }}
          >
        <CloseIcon fontSize="inherit" />
        </IconButton>
        }
        >
           <AlertTitle><b> Aviso </b></AlertTitle>
          {ValueMensajeRest}
        </Alert>
          
          

        </Collapse>

        {/* Alerta de error  */}

        <Collapse in={openError}>
          <Alert severity="error"
          action={
            <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={() => {
            setOpenError(false);
              }
            }
            >
      <CloseIcon fontSize="inherit" />
      </IconButton>
      }
      >
         <AlertTitle><b> Error </b></AlertTitle>
        {ValueMensajeRest}
      </Alert>
      
      </Collapse>

        </div>
      </div>


</Container>
     
  );
}


export default CRUDMOTIVOMOVIMIENTO;