import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTableUsuarios from './../components/ViewRegistros/MaterialTableUsuario'
import {getPostsUsuario} from './../utils/RestUsuario'


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoUsuarios extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }


  async  componentDidMount() {
        
    const UsuarioFilters = {
        UsuarioId : 0,
        UsuarioNombre : "",
    }

    await getPostsUsuario(UsuarioFilters)
    .then((res) => {
        console.log(res.data)
        this.setState({
            
            posts: res.data.SDTUsuarios,
            loading : false,
        });

    })
    .catch((err) => console.log("Error: " + err));
};

 render() {

  const {posts} = this.state;
 
    return (
       <Container>
        
        <MaterialTableUsuarios
         posts = {posts}
         />
      </Container>
 
    );
  }
}
export default CatalogoUsuarios;