import axios from 'axios';
import ipInfoLab from './../containers/Functions/GetIpInfolab';

export function getPostsTipoProducto(TipoProductoFilters)
{
    const {TipoProductoId, TipoProductoNombre} = TipoProductoFilters;

    const urlRest = ipInfoLab + '/rest/CatalogoTipoProducto?TipoProductoId=' + TipoProductoId + '&TipoProductoNombre=' + TipoProductoNombre
    axios.delete(urlRest)
    return axios.get(urlRest)
}