import React, { Component } from 'react';
import styled from 'styled-components';
import MaterialTablePerfiles from './../components/ViewRegistros/MaterialTablePerfiles'
import {getPostsPerfil} from './../utils/RestPerfil'


const Container = styled.div`
margin: 1%;
width:100%;
overflow: auto;
`;


class CatalogoPerfiles extends Component {

  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }


  async  componentDidMount() {
        
    const PerfilFilters = {
        PerfilId : 0,
        PerfilNombre : "",
    }

    await getPostsPerfil(PerfilFilters)
    .then((res) => {

        this.setState({
            posts: res.data.SDTPerfiles,
            loading : false,
        });

    })
    .catch((err) => console.log("Error: " + err));
};

 render() {

  const {posts} = this.state;
 
    return (
       <Container>
        
        <MaterialTablePerfiles
         posts = {posts}
         />
      </Container>
 
    );
  }
}
export default CatalogoPerfiles;