import React, {useState, useEffect} from 'react';
import MaterialTable from 'material-table';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import { makeStyles } from '@material-ui/core/styles';
import ButtonNew from '../Buttons/ButtonNewRegistro'//'././Buttons/ButtonNewRegistro';
import MenuPopupState from './../../MenuGrids/MenuMaterialTableCatalogos'
import useToken from './../Login/useToken';
import {GetPermisosAccionPantallaPerfil} from './../../containers/Functions/GetPermisosAccionPantallaPerfil';


const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  
    table: {
      minWidth: 700,
    },
  
    margin: {
      margin: theme.spacing(1),
      backgroundColor: '#ecf6fd',
      border: 0,
      fontWeight: 'bold',
      color: '#2797ef',
    },
  
  }));

const MaterialTableProveedores = React.memo((props) =>
  {
    const {posts} =  props;
    

    const classes = useStyles();

    const [dataMaterialTable, setdataMaterialTable] = useState([])

    //Se obtienen los datos del usuario logueado
    const {TokenSdtUsuario} = useToken();
    const {UsuarioId, PerfilId} =  TokenSdtUsuario

    //Variables donde se guardan las acciones permitidas dentro de la pantalla
    const [PermisoAGREGARPROVEEDOR, setPermisoAGREGARPROVEEDOR] = useState(false)
    const [PermisoMODIFICARPROVEEDOR, setPermisoMODIFICARPROVEEDOR] = useState(false)
   

    useEffect (() => {
      //Servicio para recuperar las acciones permitidas del usuario [INICIO]
      const FiltersPermisos = {
        PerfilId : PerfilId,
        OpcionId : 0,
        OpcionUrl : '/CatalogoProveedores'
      }

      const PermisosMenu =  GetPermisosAccionPantallaPerfil(FiltersPermisos)
      .then((response) => {

        return {response}

      })

      var getPermisos = Promise.resolve(PermisosMenu);
      getPermisos.then(function(value) {

      const {SDTPerfilUsuarioOpcion} =  value.response.data
      const {Accion} = SDTPerfilUsuarioOpcion


      
      const DataWithMenu = posts.map(Response => {
      const {ProveedorId} = Response;
    
      


      //Se arman las opciones que tendr el men delmaterial table
      let OpcionesMenuMaterialTable = []
      var {permisoModificar} = false
      
      Accion.map((accion) => {
        console.log(accion)
        const {AccionId, AccionAcceso} = accion

        switch (AccionId) {
          case "AGREGARPROVEEDOR":
           setPermisoAGREGARPROVEEDOR(AccionAcceso)
           break;

         case "MODIFICARPROVEEDOR":
          permisoModificar = AccionAcceso
          setPermisoMODIFICARPROVEEDOR(AccionAcceso)
          break;
        
         default:
           break;
        }

      })

      OpcionesMenuMaterialTable = [
        {
          pathname : "/ViewProveedor",
          state : [{
            Mode : "DSP",
            id : ProveedorId,
          }],
      
          TituloOpcion : "Visualizar"
        }
      ]

      if(permisoModificar)
      {
        let datosmenu = [...OpcionesMenuMaterialTable,
          {
            pathname : "/UpdateProveedor",
             state : [{
               Mode : "UPD",
               id : ProveedorId,
             }],
  
             TituloOpcion : "Modificar"
          }
         ]

         OpcionesMenuMaterialTable = datosmenu
      }



      return{...Response, DataWithMenu: <MenuPopupState OpcionesMenuMaterialTable= {OpcionesMenuMaterialTable}> </MenuPopupState>}
    
    })
      //Servicio para recuperar las acciones permitidas del usuario [FIN]
    
      setdataMaterialTable(DataWithMenu)
    })

    }, [props])

    // const DataWithMenu = posts.map(Response => {
    //   const {ProveedorId} = Response;
    
    //   return{...Response, DataWithMenu: <MenuPopupState FProveedorId = {ProveedorId}> </MenuPopupState>}
    // })

    // const dataMaterialTable =  DataWithMenu;

    return(
        <div>
            <MaterialTable
                title={
                    <>Proveedores
                    {
                      PermisoAGREGARPROVEEDOR &&
                      <ButtonNew
                      UrlButton = {"/NuevoProveedor"}
                      TittleButton = {"Agregar Proveedor"}
                      Mode = "INS"
                      />
                    }
                    
                   </>
                }

                columns = {[
                    { title: 'ProveedorNombre', field: 'ProveedorNombre' },
                    { title: 'ProveedorRazonSocial', field: 'ProveedorRazonSocial' },
                    { title: 'ProveedorDireccion', field: 'ProveedorDireccion' },
                    { title: 'ProveedorTelefono', field: 'ProveedorTelefono' },
                    { title: 'ProveedorRepresentanteLegal', field: 'ProveedorRepresentanteLegal' },
                    { title: '', field: 'DataWithMenu',
                    cellStyle: {
                      width: '1%',
                   
                      },
                    },
                    
                    
                    
                ]}

                data= {dataMaterialTable}//{posts}

                options={{
                      
                     headerStyle: {
                     backgroundColor: '#4FC3A1',
                     color: '#ffffff',
                     padding: 8,
                     position: 'sticky', 
                     top: 0
                     },
                     rowStyle: {
                     fontSize:16,
                     height:10,
                     width:20,
                    },
                    cellStyle: {
                     width: 200,
                     
                   },
                   
                }}

                localization={{
                    toolbar: {
                      searchPlaceholder: 'Buscar Proveedor',
                      
                    },
            
                    pagination: {
                      labelDisplayedRows: '{from}-{to} de {count}',
                      labelRowsSelect: 'Proveedores por pgina',
                  },
                }}
            />
        </div>
    )
  })

  export default MaterialTableProveedores;