import React, { Component } from 'react';
import styled from 'styled-components';
import { getPostsTipoProducto } from './../../utils/RestTipoProducto';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
}));



class ComboBoxTipoProducto extends Component {
    
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading: true,
    };
  }


componentDidMount() {

  const TipoProductoFilters = {
    TipoProductoId : 0,
    TipoProductoNombre: "",
    MostrarDefault: false
    
  }

      getPostsTipoProducto(TipoProductoFilters)

      .then((res) => {
        this.setState({
          posts: res.data,
          loading: false,
        });
      })
      .catch((err) => console.log(err));

     
  }

renderPosts = () => {
    const { posts } = this.state;
    
    
    return posts.map(post => {
      const { TipoProductoNombre, TipoProductoId } = post;
     
        return (
          <option value={TipoProductoId} >{TipoProductoNombre}</option>
        );
     

    //   this.props.parentCallback(TipoProductoId)

    });

    
  }

// renderFilters = () => {
//   const classes = useStyles();
  

// }

render() {
    const { loading } = this.state;
    
    const getTipoProductoValue = event => {
      const TipoProductoValue = event.target.value;
      this.props.parentCallback(TipoProductoValue)

    }


    return (
        
    //  <div>

         
         <FormControl variant="outlined" className="makeStyles-field" fullWidth>
           
             <InputLabel htmlFor="outlined-age-native-simple" >Tipo</InputLabel>
                        <Select
                        native
                        onChange={getTipoProductoValue}
                        label="Tipo de Producto"
                        inputProps={{
                        name: 'TipoProducto',
                        id: 'outlined-age-native-simple',
                        
                        }}
                        >

                      <option aria-label="None" value={0} />
                      {this.renderPosts()}
                      </Select>
                      
             </FormControl>
  
    //  </div>
      
    
    );
  }
}

export default ComboBoxTipoProducto;