import React, {Component} from 'react'
import TextField from '@material-ui/core/TextField';
import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import deLocale from "date-fns/locale/es";
import DateFnsUtils from '@date-io/date-fns';
import FormControl from '@material-ui/core/FormControl';
import ComboBoxProveedor from './../../../../containers/ComboBox-Data/ComboBoxProveedor'
import ComboBoxMotivoMovimiento from './../../../../containers/ComboBox-Data/ComboBoxMotivoMovimiento'

class ModalEntradaSalidaProducto extends Component{
    constructor(props){
        super(props);
        this.state = {
            valueCodigobarra : "",
            ValueProveedor : 0,
            ValueUbicacion : "",
            selectedDateCaducidad : null,
            selectedDateKardexFechaMovimientoFisico : new Date(),
            MotivoMovimientoTipo: this.props.MotivoMovimientoTipo
        }
    }

    getCodigoBarra = (e) => {
        const codigobarravalue = e.target.value
        this.setState({valueCodigobarra : codigobarravalue})
        this.props.callbackValueCodigoBarra(codigobarravalue)
    }

    callbackProveedorFunction = (ProveedorData) => {
        const messageProveedor = ProveedorData
        this.setState({ValueProveedor:messageProveedor})
        this.props.callbackValueProveedor(messageProveedor)

    }


    getCantidad = (e) =>{
        const CantidadValue = e.target.value
        this.props.callbackValueCantidad(CantidadValue)
    }

    callbackMotivoMovimientoFunction = (MotivoMovimientoData) => {
        this.props.callbackValueMotivoMovimiento(MotivoMovimientoData)
    }

    getUbicacion = (e) => {
        const UbicacionValue = e.target.value
        this.setState({ValueUbicacion : UbicacionValue})
        this.props.callbackValueUbicacion(UbicacionValue)
    }

    handleDateChangeCaducidad = (date) => {
        this.setState({selectedDateCaducidad : date})
        this.props.callbackValueFechaCaducidad(date)

      };

    handleDateChangeKardexFechaMovimientoFisico = (date) => {
        this.setState({selectedDateKardexFechaMovimientoFisico : date})
        this.props.callbackValueFechaMovimientoFisico(date)

      };

    getObservaciones = (e) => {
        const KardexObservaciones = e.target.value
        this.props.callbackValueObservaciones(KardexObservaciones)
      }
      
    

    render(){

        const {valueCodigobarra, selectedDateCaducidad, selectedDateKardexFechaMovimientoFisico, MotivoMovimientoTipo} = this.state

        return(
            
        <div>
            <h3 style = {{height:"10px"}}>Producto</h3>

            <TextField 
            id="CodigoBarra" 
            label="Cdigo de Barra" 
            variant="outlined" 
            className="makeStyles-field" 
            fullWidth={true} 
            onBlur = {this.getCodigoBarra}
            />

            <FormControl variant="outlined" className="makeStyles-field"  fullWidth={true} style = {{height:"35px"}}>
                    
                <ComboBoxProveedor 
                fullWidth={true} 
                parentCallback = {this.callbackProveedorFunction}
                ProductoCodigoBarra = {valueCodigobarra}
                />
        
            </FormControl>

            <h3 style = {{height:"10px"}}>Informacin</h3>

            <TextField
            id="Cantidad"
            label="Cantidad"
            type="number"
            InputLabelProps={{
            shrink: true,
            }}
            variant="outlined"
            onChange = {this.getCantidad}
            className="makeStyles-field"
            />

            <FormControl variant="" className="makeStyles-field"  fullWidth={false}>
                    
                <ComboBoxMotivoMovimiento 
                fullWidth={false} 
                parentCallback = {this.callbackMotivoMovimientoFunction}
                MotivoMovimientoTipo = {MotivoMovimientoTipo}/>
                  
            </FormControl>

            {MotivoMovimientoTipo == "E" && 
            <TextField 
            id="Ubicacion" 
            label="Ubicacin" 
            variant="outlined" 
            className="makeStyles-field" 
            fullWidth={true} 
            onChange = {this.getUbicacion}
            /> 
            }
            

            <MuiPickersUtilsProvider locale={deLocale} utils={DateFnsUtils} position="fixed" className="makeStyles-field" fullWidth={true}>

                {MotivoMovimientoTipo == "E" &&
                <KeyboardDatePicker
                margin="normal"
                autoOk = {true}
                id="date-picker-dialog-Caducidad"
                label="Caducidad"
                format="dd/MM/yyyy"
                value={selectedDateCaducidad}
                onChange={this.handleDateChangeCaducidad}
                KeyboardButtonProps={{
                    'aria-label': 'change date',
                }}
                />
                }


                <KeyboardDatePicker
                margin="normal"
                autoOk = {true}
                id="date-picker-dialog-Movimiento"
                label="Fecha del Movimiento"
                format="dd/MM/yyyy"
                value={selectedDateKardexFechaMovimientoFisico}
                KeyboardButtonProps={{
                    'aria-label': 'change date',
                }}
                onChange={this.handleDateChangeKardexFechaMovimientoFisico}
                />

            </MuiPickersUtilsProvider>


            <TextField
            id="Observaciones"
            label="Observaciones"
            multiline
            rows={3}
            columns = {4}
            variant="outlined"
            fullWidth={true}
            onChange = {this.getObservaciones}
            />
        </div>
        )

    }

}

export default ModalEntradaSalidaProducto